/**
 * Class representing a EventObserver.
 */
class EventObserver {
    /**
     * Creates an instance of a EventObserver
     */
    constructor() {
        this.handlers = [];
    }

    /**
     * Adds a Handler to the Event
     */
    addHandler(handler) {
        this.handlers.push(handler);
    }

    /**
     * Notifies all Handlers
     */
    trigger(...params) {
        this.handlers.forEach(handler => { handler(...params); });
    }
}

export default EventObserver;