// TODO - CHECK EVENT HANDLER

import EventObserver from "../../EventObserver";

class EditorScreen {

    /**
     * The Editor Screen controls and monitors the mouse clicks on the process
     */
    constructor() {
        this._screen = null;
        this._processScreen = null;
        this._position = { top: 0, left: 0, x: 0, y: 0 };
        this._savePosition = { top: 0, left: 0 };
        this._control = false;
        this._clickedTarget = null;
        this._moving = false;

        this.onMouseMove = this.onMouseMove.bind(this);
        this.onMouseUp = this.onMouseUp.bind(this);

        this.setMoving = new EventObserver();
    }

    get screen() {
        return this._screen;
    }

    set screen(screen) {
        this._screen =screen;
    }

    get processScreen() {
        return this._processScreen;
    }

    set processScreen(processScreen) {
        this._processScreen = processScreen;
    }

    get position() {
        return this._position;
    }

    set position(p) {
        this._position = p;
    }

    get savePosition() {
        return this._savePosition;
    }

    set savePosition(p) {
        this._savePosition = p;
    }

    get control() {
        return this._control;
    }

    set control(value) {
        this._control = value;
    }

    get clickedTarget() {
        return this._clickedTarget;
    }

    set clickedTarget(value) {
        this._clickedTarget = value;
    }

    /**
     * Controls the control variable. If control equals true, the drag and drop function can be used
     *
     * @param {Node} target An element selected in the process
     */
    updateClickedTarget(target) {
        if (!target) { this.control = true; }
        else { this.control = false; }
        this.clickedTarget = target;
    }

    /**
     * Set an anchor for the position of the process
     */
    anchor() {
        this.savePosition = { top: this.screen.scrollTop, left: this.screen.scrollLeft };
    }

    /**
     * Entry function for the drag and drop function.
     *
     * @param {HTMLElement} screen html element of the process
     * @param {number} x x coordinate of the mouse position
     * @param {number} y y coordinate of the mouse position
     */
    mouseDownHandler(screen, x, y) {
        if (!this.control) return;
        this.screen = screen;
        this.position = {top: screen.scrollTop, left: screen.scrollLeft, x: x, y: y};

        this.screen.addEventListener("mousemove", this.onMouseMove);
        document.addEventListener("mouseup", this.onMouseUp);
    };

    /**
     * ends the drag and drop function
     */
    onMouseUp(e) {
        this.setMoving.trigger(false);
        this.screen.removeEventListener("mousemove", this.onMouseMove);
        document.removeEventListener("mouseup", this.onMouseUp);
    }

    /**
     * updates the process position while the mouse pointer is move
     */
    onMouseMove(event) {
        
        if (!this.control) { return }

        this.setMoving.trigger(true);

        // How far the mouse has been moved
        const dx = event.clientX - this.position.x;
        const dy = event.clientY - this.position.y;
            
        // Scroll the element
        this.screen.scrollTop = this.position.top - dy;
        this.screen.scrollLeft = this.position.left - dx;
    }

    /**
     * Memory the current position of the processes
     */
    saveCurrentPosition() {
        if (this.screen != null) {
            console.log(this.screen.scrollTop);
            this.savePosition = {top: this.screen.scrollTop, left: this.screen.scrollLeft};
            console.log(this.savePosition);
        }
    }

    /**
     * Changes the current position of the processes.
     */
    setPosition() {
        if (this.screen != null) {
            this.screen.scrollTop = this.savePosition.top;
            this.screen.scrollLeft = this.savePosition.left;
        }
    }

    /**
     * Sets the current position of the processes to 0
     */
    initPosition() {
        if (this.screen != null) {
            this.screen.scrollTop = 0;
            this.screen.scrollLeft = 0;
        }
    }

    /**
     * Controller for the drag and drop function
     *
     * @param {HTMLElement} screen Element to be moved by the drag and drop function
     */
    startEditorScreen(screen) {

        if (screen == null) return null;
        this.screen = screen;
        this.control = true;
        screen.addEventListener('mousedown', (event) => {
            if (!this.control) return;
            this.mouseDownHandler(screen, event.clientX, event.clientY);
        });
    }

}

export default EditorScreen;