class Area {

    constructor(id, parent) {
        this._id = id;
        this._parent = parent;
        this._memories = [];
    }

    get id() {
        return this._id;
    }
    
    set id(id) {
        this._id = id;
    }

    get parent() {
        return this._parent;
    }
    
    set parent(parent) {
        this._parent = parent;
    }

    get memories() {
        return this._memories;
    }

    set memories(m) {
        this._memories = m;
    }

    findMemory(id) {
        return this.memories.findIndex((m) => m.id == id ); 
    }

    addMemory(memory) {        
        let pos = this.memories.findIndex((m) => m.category == memory.category );
        if (pos == -1) {
            this.memories.push(memory);
        }
        else {
            let newInputs = this.memories[pos].inputs.concat(memory.inputs);
            let newOutputs = this.memories[pos].outputs.concat(memory.outputs);
            newInputs = [...new Set([...this.memories[pos].inputs,...memory.inputs])];
            newOutputs = [...new Set([...this.memories[pos].outputs,...memory.outputs])];
            this.memories[pos].inputs = newInputs;
            this.memories[pos].outputs = newOutputs;
        }
    }

    removeMemory(id) {
        this.memories = this.memories.filter( m => m.id != id);
    }

    removeRelationFromMemory(id, relation, type) {
        let size = 0;
        let pos = this.findMemory(id);
        if (pos > -1 ) {
            if (type == "input") {
                this.memories[pos].removeInput(relation);
            }
            else if (type == "output") {
                this.memories[pos].removeOutput(relation);
            }
            size = this.memories[pos].inputs.length + this.memories[pos].outputs.length
        }
        return size;
    }

    areaSerialize() {
        return {id: this.id, parent: this.parent, memories: this.memoriesSerialize()};
    }
    
    memoriesSerialize() {
        let serialized = [];
        this.memories.forEach( memory => {
            serialized.push(memory.memorySerialize());
        });
        return serialized;
    }

    removeActivityRelation(id) {
        this.memories.forEach( memory => {
            memory.removeInput(id);
            memory.removeOutput(id);
        });
    }
}

export default Area;