import View from '../../View'
import './zoom.css'

import plusIcon from '../../../svg/plus.svg';
import minusIcon from '../../../svg/minus.svg';
import fullIcon from '../../../svg/full.svg';

import EventObserver from '../../EventObserver';
import Zoom from './Zoom';

class ZoomView extends View {

    /**
     * Creates the user interface of the zoom function
     */
    constructor() {
        super();
        this._zoom = null;
        this.zoomIn = new EventObserver();
        this.zoomOut = new EventObserver();
        this.initPosition = new EventObserver();
        this.oldPosition = new EventObserver();
    }

    get zoom() {
        return this._zoom;
    }

    set zoom(zoom) {
        this._zoom = zoom;
    }

    /**
     * creates the HTML user interface
     *
     * @param {HTMLElement} parentDOM DOM parent node of the user interface.
     * @param {Zoom} zoom Zoom object with which the ZoomView forms a module.
     * @return {HTMLElement} User interface of the zoom function
     */
    render(parentDOM, zoom) {

        this.zoom = zoom;

        // container
        let zoomBar = document.createElement("div");
        zoomBar.id = "zoom";

            // see full graph button
            const full = document.createElement("div");
            full.id = "full";
            full.classList.add("btn");
            full.setAttribute("alt", "Show full process");
            zoomBar.appendChild(full);
            full.addEventListener('click', (event) => {
                this.initZoom(parentDOM);
            });

            const fullImg = document.createElement("img");
            fullImg.setAttribute("src", fullIcon);
            full.appendChild(fullImg);

            // zoom in button
            let plus = document.createElement("div");
            plus.id = "plus";
            plus.classList.add("btn");
            plus.setAttribute("alt", "Zoom in");
            zoomBar.appendChild(plus);
            plus.addEventListener('click', (event) => {
                this.zoomIn.trigger(this.zoom.zoomTarget);
            });

            const plusImg = document.createElement("img");
            plusImg.setAttribute("src", plusIcon);
            plus.appendChild(plusImg);

            // zoom out button
            let minus = document.createElement("div");
            minus.id = "minus";
            minus.classList.add("btn");
            minus.setAttribute("alt", "Zoom out");
            zoomBar.appendChild(minus);
            minus.addEventListener('click', (event) => {
                this.zoomOut.trigger(this.zoom.zoomTarget);
            });

            const minusImg = document.createElement("img");
            minusImg.setAttribute("src", minusIcon);
            minus.appendChild(minusImg);

        // add to parent
        parentDOM.appendChild(zoomBar);

        // set zoom to full height 
        this.oldPosition.trigger();

        return zoom;
    }

    /**
     * Sets the zoom to the height of the process
     *
     * @param {HTMLElement} parentDOM Parent node of the process.
     */
    initZoom(parentDOM, direction = 0) {

        const parentHeight = parentDOM.offsetHeight;
        const processHeight = this.zoom.zoomTarget.offsetHeight * (this.zoom.zoomValue / 10);

        if (parentHeight < processHeight) {
            if (direction == 0 || direction == -1) {
                this.zoomOut.trigger(this.zoom.zoomTarget);
                this.initPosition.trigger();
                this.initZoom(parentDOM, -1);
            }
        }
        else {
            if (direction == 0 || direction == 1) {
                this.zoomIn.trigger(this.zoom.zoomTarget);
                this.initPosition.trigger();
                this.initZoom(parentDOM, 1);
            }
        }
    }

    /**
     * shows the zoom user interface when the visibility is true
     *
     * @param {HTMLElement} parentDOM DOM parent node of the user interface.
     * @param {Zoom} zoom Zoom object with which the ZoomView forms a module.
     * @return {HTMLElement} User interface of the zoom function
     */
    toggle(parentDOM, zoom) {
        this.root.innerHTML = "";
        if (this.visible) { return this.render(parentDOM, zoom); } 
        else { return null; }
    }
}

export default ZoomView;