import View from "../../View";
import EventObserver from '../../EventObserver';
import "./contextFactors.css";

import closeIcon from '../../../svg/close.svg';

class ContextFactorsView extends View {

    /**
     * Creates an instance of the context faxtors form
     *
     * @param {Object} contextFactors Inputs of the formluar
     */
    constructor(contextFactors) {
        super();
        this._contextFactors = contextFactors;

        this.closeContextFactors = new EventObserver();
        this.nextContextFactor = new EventObserver();
        this.lastContextFactor = new EventObserver();
        this.makeDecision = new EventObserver();
        this.submitContextFactors = new EventObserver();
    }

    get contextFactors() {
        return this._contextFactors;
    }

    /**
     * creates the context factors form as html element
     *
     * @return {HTMLElement}
     */
    render() {

        // GET DATA FROM contextFactores
        const input = this.contextFactors.getInputByPointer();
        const counter = this.contextFactors.decisionPointer;
        const maximum = this.contextFactors.inputs.length;
        const statusLength = (counter / maximum) * 100 + "%";
        const decisionValues = this.contextFactors.decisionValues;

        // HEADER
        const contextFactors = this.root;
        contextFactors.id = "contextFactors";
        this.parentDOM.appendChild(contextFactors);
        
        const dialog = document.createElement("div");
        dialog.classList.add("dialog");
        contextFactors.appendChild(dialog);
        
        const header = document.createElement("div");
        header.classList.add("header");
        dialog.appendChild(header);

        const title = document.createElement("span");
        title.textContent = "Neuer Prozess erstellen";
        header.appendChild(title);
       
        const closeBtn = document.createElement("img");
        closeBtn.classList.add("icon");
        closeBtn.setAttribute("src", closeIcon);
        header.appendChild(closeBtn);
        closeBtn.addEventListener('click', (event) => {
            this.closeContextFactors.trigger(this.parentDOM);
        });
       
        // MAIN
        const main = document.createElement("div");
        main.classList.add("main");
        dialog.appendChild(main);

        const bar = document.createElement("div");
        bar.classList.add("bar");
        main.appendChild(bar);

        const barStatus = document.createElement("span");
        barStatus.textContent = counter + " of " + maximum;
        barStatus.style.width = statusLength;
        bar.appendChild(barStatus);

        if (input != null) {
            const context = document.createElement("p");
            context.innerHTML = input.context;
            main.appendChild(context);
            
            const optionForm = document.createElement("form");
            main.appendChild(optionForm);
            
            for (let i = 0; i < input.options.length; i++) {
                
                const optionContainer = document.createElement("div");
                optionForm.appendChild(optionContainer);

                const optionInput = document.createElement("input");
                optionInput.setAttribute("type", "radio");
                optionInput.setAttribute("name", input.id);
                optionInput.setAttribute("id", input.id + i);
                optionInput.setAttribute("value", i);
                optionContainer.appendChild(optionInput);
                (decisionValues.get(input.id) == i) ? optionInput.checked = true : null;
                optionInput.addEventListener('change', () => {
                    this.makeDecision.trigger(input.id, optionInput.value);
                });

                const optionLabel = document.createElement("label");
                optionLabel.textContent = input.options[i];
                optionLabel.setAttribute("for", input.id + i);
                optionContainer.appendChild(optionLabel);
            }
        }
        else {
            const welcome = document.createElement("p");
            welcome.innerHTML = 'Beantworten Sie die folgenden <b>13 Fragen</b>. Das Programm erstellt dann einen hybriden Entwicklungsprozess basierend auf ihren Antworten.<br/><br/>Drücken Sie auf <b>[ Weiter ]</b>, um zur ersten Frage zu gelangen.';
            main.appendChild(welcome);
        }

        // FOOTER
        const footer = document.createElement("div");
        footer.classList.add("footer");
        dialog.appendChild(footer);

        const backBtn = document.createElement("button");
        backBtn.textContent= "Zurück";
        footer.appendChild(backBtn);
        backBtn.addEventListener('click', (event) => {
            this.lastContextFactor.trigger(this.parentDOM);
        });
        (counter == 0) ? backBtn.disabled = true : backBtn.disabled = false;

        const nextBtn = document.createElement("button");
        footer.appendChild(nextBtn);
        if (counter < maximum) {
            nextBtn.textContent = "Weiter";
            nextBtn.addEventListener('click', (event) => {
                this.nextContextFactor.trigger(this.parentDOM);
            });
        }
        else {
            nextBtn.textContent = "Prozess erstellen";
            nextBtn.addEventListener('click', (event) => {
                this.submitContextFactors.trigger(this.parentDOM);
            });
        }

        return contextFactors;

    }

}

export default ContextFactorsView;