import View from '../../View';
import EventObserver from '../../EventObserver';
import './startScreen.css';


class StartScreenView extends View {

    /**
     * Creates the start screen instance of the editor
     *
     */
    constructor() {
        super();

        this.clickImport = new EventObserver();
        this.clickCreateNew = new EventObserver();
    }

    /**
     * Creates the start screen of the editor
     *
     * @return {HTMLElement} User interface of the start screen
     */
    render() {

        const startScreen = this.root;
        startScreen.id = "startScreen";
        this.parentDOM.appendChild(startScreen);

        const noProcessMsg = document.createElement("div");
        noProcessMsg.id = "noProcessMsg";
        startScreen.appendChild(noProcessMsg);

        const h1 = document.createElement("h1");
        h1.textContent = "No Development Process loaded";
        noProcessMsg.appendChild(h1);

        const p = document.createElement("p");
        p.textContent = "Load a Development Process from your PC or create a new one.";
        noProcessMsg.appendChild(p);

        const btnContainer = document.createElement("div");
        btnContainer.classList.add("btnContainer");
        noProcessMsg.appendChild(btnContainer);

        const fileLoader = this.getFileLoader(this.clickImport);
        btnContainer.appendChild(fileLoader);
        
        const loadBtn = document.createElement("button");
        loadBtn.textContent = "Load Process";
        btnContainer.appendChild(loadBtn);
        loadBtn.addEventListener('click', () => {
            document.getElementById("fileLoader").click();
        });

        const createBtn = document.createElement("button");
        createBtn.textContent = "Create new";
        btnContainer.appendChild(createBtn);
        createBtn.addEventListener('click', () => {
            this.clickCreateNew.trigger(this.parentDOM);
        });

        return startScreen;
    }

}

export default StartScreenView;