import View from '../../View'
import './editorScreen.css'

import EventObserver from '../../EventObserver';

    /**
     * Returns x raised to the n-th power.
     *
     * @param {number} x The number to raise.
     * @param {number} n The power, must be a natural number.
     * @return {number} x raised to the n-th power.
     */
class ScreenView extends View {

    constructor(editorScreen) {
        super();
        this._editorScreen = editorScreen;

        this.clicked = new EventObserver();
        this.stopMoveScreen = new EventObserver();

    }

    get editorScreen() {
        return this._editorScreen;
    }

    /**
     * Returns x raised to the n-th power.
     *
     * @param {number} x The number to raise.
     * @param {number} n The power, must be a natural number.
     * @return {number} x raised to the n-th power.
     */
    render(/*parentDOM*/) {

        //create a <div> for the ..
        const screen = this.root;
        screen.id = "editorScreen";

        // Add to parent
        this.parentDOM.appendChild(screen);

        this.root.addEventListener('click', (event) => {
            let target = null;
            if (event.target.classList.contains("memory")) {
                target = event.target;
            }
            else if (event.target.classList.contains("activityButton")) {
                target = event.target.closest(".activity");
            }
            this.clicked.trigger(target);
            this.handleCSS(target, screen);

        });

        // stop click-and-drag if mouseup
        window.addEventListener('mouseup', (event) => {
            this.stopMoveScreen.trigger();
        });

        window.addEventListener('dragend', () => {
            this.zonesInBackground();
            this.areasInBackground();
            let memories = document.getElementsByClassName("memory");
            for (let m of memories) { m.classList.remove("noPointerEvents"); }
        });

        return screen;
    }

    /**
     * Returns x raised to the n-th power.
     *
     * @param {number} x The number to raise.
     * @param {number} n The power, must be a natural number.
     * @return {number} x raised to the n-th power.
     */
    handleCSS(target, screen) {
        if (target) { 
            screen.classList.add('blocked'); 

            if (target.classList.contains('memory')) { 
                this.zonesInForeground(); 
            }
            //else if (target.classList.contains('newMemory')) { this.zonesInForeground(); }
            else { 
                this.zonesInBackground();
                this.areasInBackground();
            }
        }
        else { 
            screen.classList.remove('blocked'); 
            this.zonesInBackground();
            this.areasInBackground();
        }
    }

    /**
     * Returns x raised to the n-th power.
     *
     * @param {number} x The number to raise.
     * @param {number} n The power, must be a natural number.
     * @return {number} x raised to the n-th power.
     */
    // Drag new Memories / activities
    dragAction(obj) {

        // Leiste 
        if (obj.data == null) {

            if (obj.instruction.indexOf("Memory") != -1) {
                this.zonesInForeground();
            }
            else if (obj.instruction.indexOf("Activity") != -1) {
                this.areasInForeground();
            }
        }
        // existierende Memories
        else {

            // CHANGE CSS FOR Verschieben
            const target = this.editorScreen.clickedTarget;
            const area = target.closest(".area");
            area.addEventListener('dragleave', () => {
                this.editorScreen.clickedTarget.classList.add("noPointerEvents");
            }, { once: true });
            
            window.addEventListener('dragstart', () => {
                this.zonesInForeground(); 
                let memories = document.getElementsByClassName("memory");
                for (let m of memories) { 
                    if (m.id != this.editorScreen.clickedTarget.id) {
                        m.classList.add("noPointerEvents"); 
                    }
                }
            }, { once: true });

        }
    }

    /**
     * Returns x raised to the n-th power.
     *
     * @param {number} x The number to raise.
     * @param {number} n The power, must be a natural number.
     * @return {number} x raised to the n-th power.
     */
    setMoving(moves) {
        if (moves) { this.editorScreen.processScreen.style.cursor = "grabbing" }
        else { this.editorScreen.processScreen.style.cursor = "grab" }
    }

    /**
     * Returns x raised to the n-th power.
     *
     * @param {number} x The number to raise.
     * @param {number} n The power, must be a natural number.
     * @return {number} x raised to the n-th power.
     */
    updateCursor(target) {
        if (target) { this.editorScreen.processScreen.style.cursor = "default"; }
        else { this.editorScreen.processScreen.style.cursor = "grab"; 
        }
    }

}

export default ScreenView;