import View from '../../View';
import {ContainerNode, ActivityNode} from './processModules/Node';
import './process.css';

import fluidSingle from '../../../svg/fluidSingle.svg';
import fluidMultiple from '../../../svg/fluidMultiple.svg';
import solidSingle from '../../../svg/solidSingle.svg';
import solidMultiple from '../../../svg/solidMultiple.svg';
import EventObserver from '../../EventObserver';

class ProcessView extends View {

    /**
     *  Creates a process instance that is displayed in the editordisplay
     *  @param {Process} process  Process Data to be displayed
     *  @param {Zoom} zoom  Zoom instance
     */
    constructor(process, zoom, screen) {
        super();
        this._process = process;
        this._zoom = zoom;
        this._activeElement = null;

        // filter wrong Memories
        this.shiftMemory = new EventObserver();
        this.removeMemory = new EventObserver();

        // User Actions
        this.newActiveElement = new EventObserver();
        this.passDragObject = new EventObserver();
        this.addExistingMemory = new EventObserver();
        this.relocatingMemory = new EventObserver();
        this.addMemory = new EventObserver();
        this.addActivity = new EventObserver();
    }

    get process() {
        return this._process;
    }

    get zoom() {
        return this._zoom;
    }

    get activeElement() {
        return this._activeElement;
    }

    set activeElement(element) {
        this._activeElement = element;
    }

    // Render
    render() {

        //create process container
        const editor = this.root;
        editor.id = "process";
        editor.classList.add("static");
        this.parentDOM.appendChild(editor);
        
        // 1. draw phases / activities
        this.drawNode(this.process.root, null, editor);

        // 2. add areas 

            // add structure Nodes
            this.wrapAct(editor);
            this.wrapSeqAndPar(editor);

            // add areas
            this.addAreas(editor);

        // 3. add memories

            this.divideAreas(this.process.relations); 
            this.addMemories(this.process.relations);

            this.addCSS(editor);

        // 4. add relations

            // arrows
            const arrows = document.createElement("div");
            arrows.id= "arrows";
            editor.appendChild(arrows);

            const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
            svg.id = "svg";
            svg.setAttribute("stroke-sidth", "3");
            svg.setAttribute("stroke", "#000");
            arrows.appendChild(svg);

            // add relations
            this.addRelations(this.process.relations, this.parentDOM, svg);

        // control: check if ids are unique
        // console.log(this.checkUnique(editor));

        return editor;
    }

    // --------------------------------------------------------------------
    // 1. draw the ContainerNodes and ActitivityNodes from the process tree
    // --------------------------------------------------------------------

    /**
     *  Adds a node of the process tree and his children as a HTML child to the passed HTML element.
     *  This is a recursive function.
     *  @param {Node} node  Root node of the process tree
     *  @param {Node} parentNode  The parent node from the passed node
     *  @param {HTMLElement} parentDOM  The parent node in the DOM tree
     */
    drawNode(node, parentNode, parentDOM) {

        let element;
        if (node instanceof ContainerNode) {
            element = this.drawContainer(node.arrangement, node.id, node);
        }
        else if (node instanceof ActivityNode) {
            let childless = (node.children.length == 0) ? true : false;
            element = this.drawActivity(node, parentNode, childless);
        }
        else {
            element = document.createElement("p");
            element.textContent = "INVALID GRAPH FORMAT";
        }
        parentDOM.appendChild(element);

        // draw node's children
        if (node != null) {
            node.children.forEach(child => {
                this.drawNode(child, node, element);
            });
        }
    }

    /**
     *  Creates a ContainerNode as an HTML element
     *  @param {String} arrangement  The arrangement form the node. Sequential or parallel.
     *  @param {String} id  The id of the node from the process tree
     *  @param {parentDOM} node  The ContainerNode from the process tree
     */
    drawContainer(arrangement, id,  node = null) {

        const container = document.createElement("div");
        container.classList.add("container");
        container.classList.add(arrangement);
        container.setAttribute("data-arrangement", arrangement);

        if (node) { 
            container.id = node.id; 
            container.setAttribute("data-source", "static");
            container.classList.add("static");
        }
        else {
            container.id = id;
            container.setAttribute("data-source", "dynamic");
        } 

        return container;
    }

    /**
     *  Creates a ActivityNode as an HTML element
     *  @param {Node} node  The ActivityNode from the process tree
     *  @param {Node} parentNode  The parent of the ActivityNode from the process tree
     *  @param {boolean} childless  True if the ActivityNode is childless
     */
    drawActivity(node, parentNode, childless) {

        // create base elements
        const activity = document.createElement("div");
        activity.classList.add("activity", "static");
        activity.classList.add(node.aggregate);
        activity.setAttribute("data-source", "static");
        activity.style.background = this.getColor(node.color, node.levelOfDetail);
        activity.id = node.id;

        if (node.id != "root") {

            const activityName = document.createElement("h2");
            activityName.classList.add("activityButton");
            activityName.textContent = node.name;
            activity.appendChild(activityName);
            
            const inputZone = document.createElement("div");
            inputZone.classList.add("zone", "inputZone");
            inputZone.setAttribute("alt", "Input");
            activity.appendChild(inputZone);

            const outputZone = document.createElement("div");
            outputZone.classList.add("zone", "outputZone");
            outputZone.setAttribute("alt", "Output");
            activity.appendChild(outputZone);
            
            if (childless) { activity.classList.add("childless"); }

            // Click on Activity
            activityName.addEventListener('click', (event) => {
                this.newActiveElement.trigger(node);
            });

            // DRAG & DROP
            let parentDOM = document.getElementById(parentNode.id);
            let parent = parentDOM.closest(".activity");

            // INPUT HANDLING
            inputZone.addEventListener('dragenter', () => {
                inputZone.classList.add("entered");
                activity.classList.add("readyToDrop");
            });
            inputZone.addEventListener('dragleave', () => {
                inputZone.classList.remove("entered");
                activity.classList.remove("readyToDrop");
            });
            inputZone.addEventListener('dragover', (event) => { 
                event.preventDefault(); 
            });
            inputZone.addEventListener('drop', () => {
                inputZone.classList.remove("entered");
                let instruction = this.process.dragObject.instruction;
                if (instruction == "duplicate") {
                    this.addExistingMemory.trigger(activity.id, "output", parent);
                }
                else if (instruction.includes("Memory")){
                    this.addMemory.trigger(instruction, activity.id, "output", parent);
                }
            });

            // OUTPUT HANDLING
            outputZone.addEventListener('dragenter', () => {
                outputZone.classList.add("entered");
                activity.classList.add("readyToDrop");
            });
            outputZone.addEventListener('dragleave', () => {
                outputZone.classList.remove("entered");
                activity.classList.remove("readyToDrop");
            });
            outputZone.addEventListener('dragover', (event) => { 
                event.preventDefault(); 
            });
            outputZone.addEventListener('drop', () => {
                outputZone.classList.remove("entered");
                let instruction = this.process.dragObject.instruction;
                if (instruction == "duplicate") {
                    this.addExistingMemory.trigger(activity.id, "input", parent);
                }
                else if (instruction.includes("Memory")){
                    this.addMemory.trigger(instruction, activity.id, "input", parent);
                }
            });
        }
        if (node.id == "root" && node.children.length == 0) {
            console.log("EMPTY");
        }

        return activity;
    }

    // --------------------------------------------------------------------
    // 2. Add ContainerNodes and areas to the HTML DOM
    // --------------------------------------------------------------------

    /** 
     *  Recursively goes through the process tree and wraps ActivityNodes with ContainerNodes
     *  @param {Node} node  Root node of the process tree
     */
    wrapAct(node) {
        let children = this.getChildren(node);

        if (this.isClassOf(node, "activity")) {
            let activityIdPar = node.id + "-par";
            let activityIdSeq = node.id + "-seq";
        
            if(this.isClassOf(node.parentNode, "activity")) {
                let parallel = this.drawContainer("parallel", activityIdPar);
                let sequential = this.drawContainer("sequential", activityIdSeq);
                parallel.appendChild(sequential);
                node.parentNode.insertBefore(parallel, node);
                sequential.appendChild(node);
            }
            else if (this.isClassOf(node.parentNode, "sequential")) {
                let parallel = this.drawContainer("parallel", activityIdPar);
                node.parentNode.insertBefore(parallel, node);
                parallel.appendChild(node);
            }
            else if (this.isClassOf(node.parentNode, "parallel")) {
                let sequential = this.drawContainer("sequential", activityIdSeq);
                node.parentNode.insertBefore(sequential, node);
                sequential.appendChild(node);
            }
        }

        children.forEach(child => {
            this.wrapAct(child);
        });
    }

    /** 
     *  Recursively goes through the process tree and wraps ContainerNodes with ContainerNodes
     *  @param {Node} node  Root node of the process tree
     */
    wrapSeqAndPar(node){
        let children = this.getChildren(node);
        
        let activityIdPar = node.id + "-par";
        let activityIdSeq = node.id + "-seq";

        if ((this.isClassOf(node, "parallel", "sequential")) && (this.isClassOf(node.parentNode, "activity")) && (children.length >= 2)) {
            if (this.isClassOf(node, "parallel")) {
                let sequential = this.drawContainer("sequential", activityIdSeq);
                node.parentNode.insertBefore(sequential, node);
                sequential.appendChild(node);
            }
            else if (this.isClassOf(node, "sequential")) {
                let parallel = this.drawContainer("parallel", activityIdPar);
                node.parentNode.insertBefore(parallel, node);
                parallel.appendChild(node);
            }
        }
        else if (this.isClassOf(node, "parallel") && this.isClassOf(node.parentNode, "parallel")) {
            let sequential = this.drawContainer("sequential", activityIdSeq);
            node.parentNode.insertBefore(sequential, node);
            sequential.appendChild(node);
        }
        else if (this.isClassOf(node, "sequential") && this.isClassOf(node.parentNode, "sequential")) {
            let parallel = this.drawContainer("parallel", activityIdPar);
            node.parentNode.insertBefore(parallel, node);
            parallel.appendChild(node);
        }

        children.forEach(child => {
            this.wrapSeqAndPar(child);
        });
    }

    /** 
     *  Recursively goes through the HTML DOM  and inserts the areas
     *  @param {Node} node  Root node of the HTML DOM
     */
    addAreas(node) {

        let position = 0;
        let activityId = 0;
        let children = this.getChildren(node);

        // Add Areas to childless activities
        if (this.isClassOf(node, "activity") && this.isChildless(node)) {
            activityId = node.id + "-childless";
            const area = this.drawArea("parallel", position, activityId, 0);
            node.appendChild(area);
        }

        // Add Areas to containers and activites
        else if (this.isClassOf(node, "container") && !this.isChildless(node)) {

            if(!(this.isClassOf(node, "sequential") && this.isClassOf(node.parentNode, "sequential"))) {

                let direction;
                (this.isClassOf(node, "parallel")) ? direction = "parallel" : direction = "sequential";

                // instert area before every child
                children.forEach( child => {
                        activityId = child.id;
                        const area = this.drawArea(direction, position, activityId, 0);
                        position++;
                        node.insertBefore(area, child);
                    });
                    
                // add last area element
                activityId = children[children.length - 1].id;
                const area = this.drawArea(direction, position, activityId, 1);
                node.appendChild(area);
            }
        }
        children.forEach(child => { this.addAreas(child); });
    }

    /**
     *  Creates a area as an HTML element
     *  @param {String} direction  Direction of the parent HTML DOM. Sequential or parallel
     *  @param {int} position  Position of the area within the HTML DOM parent
     *  @param {parentId} parentId  Id of the HTML DOM parent
     *  @param {int} id  Id key of the area. 0 or 1
     */
    drawArea(direction, position, parentId, id) {

        let parentDOM = document.getElementById(parentId);
        
        const area = document.createElement("div");
        area.classList.add("area", direction);
        area.setAttribute("alt", "Drop");
        area.id = parentId + "-a" + id;
        area.setAttribute("data-position", position);

        area.addEventListener('dragenter', () => {
            if (this.mayBeMoved(parentId) && (this.process.dragObject.instruction == "duplicate")) {
                area.classList.add("entered");
            }
            else if (this.process.dragObject.instruction.includes("Activity"))  {
                area.classList.add("entered");
            }

        });
        area.addEventListener('dragleave', () => {
            area.classList.remove("entered");
        });
        area.addEventListener('dragover', (event) => { 
            event.preventDefault(); 
        });
        area.addEventListener('drop', () => {
            area.classList.remove("entered");
            if (this.mayBeMoved(parentId) && (this.process.dragObject.instruction == "duplicate")) {
                this.relocatingMemory.trigger(area, parentDOM.closest(".activity").id);
            }
            else if (this.process.dragObject.instruction.includes("Activity")) {
                this.handleDropActivity(area, position, this.process.dragObject.instruction);
            }
        });
        return area;
    }

    /**
     *  Recursively goes through the HTML DOM and corrects the CSS of incorrect nodes.
     *  @param {HTMLElement} node  Root HTML element from the process DOM
     */
    addCSS(node) {
        let children = this.getChildrenAndAreas(node);

        if (this.isClassOf(node, "container", "parallel") && this.isClassOf(node.parentNode, "sequential")) {
            node.classList.add("stretch");
        }
        if (this.isClassOf(node, "activity") && this.isClassOf(node.parentNode, "sequential")) {
            node.classList.add("stretch");
        }
        if (this.isClassOf(node, "area", "parallel") && this.isClassOf(node.parentNode, "parallel") && node.firstChild == null) {
            node.classList.add("stretch");
        }

        children.forEach(child => {
            this.addCSS(child);
        });
    }

    // --------------------------------------------------------------------
    // 3. Add memories
    // --------------------------------------------------------------------

    /**
     *  Traverses the areas of the relation array and splits the memories if the area ids are not available
     *  @param {Array} relations  Relations to be represented
     */
    divideAreas(relations) {
        
        relations.forEach( area => {

                const areaDOM = document.getElementById(area.id);
                const parentDOM = document.getElementById(area.parent);
                let change = false;

                if (areaDOM && parentDOM) {
                    let parentAreas = this.getAreasFromActivity(parentDOM);
                    if ((parentAreas.findIndex((i) => i == area.id) == -1)) { change = true; }
                }
                else { change = true; }
                
                area.memories.forEach( memory => {
                    this.checkAreaIdForMemory(memory, area.id, area.parent, change);
                });
        }) 
    }

    /** 
     *  Checks if the id of the area occurs in the process
     *  @param {Memory} memory Memory whose area id is to be checked
     *  @param {String} areaId
     *  @param {Node} areaParent
     *  @param {boolean} change Should the areaid be changed?
     */
    checkAreaIdForMemory(memory, areaId, areaParent, change) {
        let area = -1;
        let parentActivity;

        let puts = [];
        memory.inputs.forEach(i => puts.push({"type": "input", "id": i}));
        memory.outputs.forEach(i => puts.push({"type": "output", "id": i}));
        puts.forEach( put => {

            if (!put) return;
            let putDOM = document.getElementById(put.id);

            if (putDOM) {
                parentActivity = putDOM.parentNode.closest('.activity');
                if (!change) {
                    if (parentActivity.id != areaParent) { change = true; }
                }
                if (change) {
                    area = this.calcArea(put.id, put.type, memory.category);
                    this.shiftMemory.trigger(memory, areaId, area, parentActivity.id, put.id, put.type);
                }
            }

            else { this.removeMemory.trigger(memory); }
        })

        return area;
    }

    /** 
     *  Calculates a suitable area for a memory in relation to an activity
     *  @param {Memory} activity Memory whose area id is to be checked
     *  @param {String} type Input or output
     *  @return {String} area id
     */
    calcArea(activity, type, category) {
        let pos = 1; (type != "input") ? pos = 1 : pos = -1;
        const aDOM = document.getElementById(activity); 
        let area;
        let areaParent;

        if (!this.nodeHasSiblings(aDOM)) { areaParent = aDOM.closest('.sequential'); }
        else if (!this.isChildless(aDOM)) { areaParent = aDOM.closest('.sequential'); }
        
        else {
            const staticParent = aDOM.parentNode.closest('.static');
            if (this.isClassOf(staticParent, 'parallel')) {
                areaParent = staticParent.parentNode.closest('.sequential');
            }
            else {
                areaParent = staticParent.parentNode.closest('.parallel');
            }
        }

        area = this.getAreaFromNode(areaParent, pos, activity);
        return area.id;
    }

    /** 
     *  Adds all memories to the proces
     *  @param {Memory} relations relations array, in which the memories are stored
     */
    addMemories(relations) {

        relations.forEach( placement => {
            if (placement.memories.length >= 1) {

                const area = document.getElementById(placement.id);
                if (area) {
                    
                    const parentNode = area.closest(".container");
                    
                    if (placement.memories.length >= 1) {
                        
                        let arrangement;
                        (this.isClassOf(parentNode, "sequential")) ? arrangement = "parallel" : arrangement = "sequential";
                        
                        const memoriesContainer = this.drawMemoriesContainer(arrangement);
                        area.appendChild(memoriesContainer);
                        
                        placement.memories.forEach( element => {
                            const memory = this.drawMemory(element);
                            memoriesContainer.appendChild(memory);
                        });
                    }
                }
            }
        });
    }

    drawMemoriesContainer(arrangement) {
        const memoriesContainer = document.createElement("div");
        memoriesContainer.classList.add("memoriesContainer");
        (arrangement == "parallel") ? memoriesContainer.classList.add("parallel") : memoriesContainer.classList.add("sequential");
        return memoriesContainer;
    }

    drawMemory(memory) {

        // icon decision
        let icon;
        if(memory.aggregate == "fluid") {
            (memory.type == "single") ? icon = fluidSingle : icon = fluidMultiple;
        } 
        else if (memory.aggregate == "solid") {
            (memory.type == "single") ? icon = solidSingle : icon = solidMultiple;
        }
        else {
            icon = fluidSingle;
        }
        let container = this.createMemory(memory, icon);
        container.setAttribute("draggable", "false");
        container.setAttribute("data-category", memory.category);

        container.addEventListener('click', (event) => {
            this.newActiveElement.trigger(memory);
        });

        container.addEventListener('dragstart', (event) => {
            this.passDragObject.trigger({"instruction": "duplicate", "data": memory});
        });

        return container;
    }

    createMemory(memory, icon) {
        let container = document.createElement("div");
        container.classList.add("memory", memory.category);
        container.id = memory.id;
    
        let img = document.createElement("img");
        img.classList.add("memoryIcon");
        img.setAttribute("src", icon);
        container.appendChild(img);

        let name = document.createElement("span");
        name.classList.add("name");
        name.textContent = memory.name;
        container.appendChild(name);
        return container
    }

    // --------------------------------------------------------------------
    // 4. Add relations
    // --------------------------------------------------------------------

    /** Creates an HTML element of type SVG in which all relationships between memories and activities are indicated by arrows
     *  @param {HTMLElement} parentDOM  Parent HTML element of the new svg 
     *  @returns {HTMLElement} 
     */
    drawRelations(parentDOM) {
        const arrows = document.createElement("div");
        arrows.id= "arrows";
        parentDOM.appendChild(arrows);

        const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
        svg.id = "svg";
        svg.setAttribute("stroke-sidth", "3");
        svg.setAttribute("stroke", "#000");
        arrows.appendChild(svg);
        
        // ADD RELATIONS
        this.addRelations(this.process.relations, parentDOM, svg);

        return arrows;
    }

    /** Adds all relationship of the process to the passed html element
     *  @param {Array} relations  Relations of the process
     *  @param {HTMLElement} parentDOM  Parent HTML element of the HTML SVG element
     *  @param {HTMLElement} svg  Parent HTML SVG element for the relations
     */
    addRelations(relations, parentDOM, svg) {

        relations.forEach( placement => {
            
            placement.memories.forEach( element => {
                const memory = document.getElementById(element.id);

                element.inputs.forEach(input => {
                    const activity = document.getElementById(input);
                    if (memory && activity) {
                        let aggregate; (this.isClassOf(activity, "fluid")) ? aggregate = "fluid" : aggregate = "solid";
                        this.drawRelation(activity, memory, aggregate, parentDOM, svg, "output");
                    }
                });
                element.outputs.forEach(output => {
                    const activity = document.getElementById(output);
                    if (memory && activity) {
                        this.drawRelation(memory, activity, element.aggregate, parentDOM, svg, "input");
                    }
                });

            });
        });
    }

    /** Adds a relationship between two HTML elements to a given html svg element.
     *  @param {HTMLElement} from  Starting point of the relationship
     *  @param {HTMLElement} to  End point of the relationship
     *  @param {String} aggregate  Agregate state of the starting point
     *  @param {HTMLElement} parentDOM  Parent HTML element of the HTML SVG element
     *  @param {HTMLElement} svg  Parent HTML SVG element for the relations
     *  @param {String} direction  Direction of the memory. Input or Output
     */
    drawRelation(from, to, aggregate, parentDOM, svg, direction) {

        // get zoom relation
        let zoom = Number(this.zoom.zoomValue)/10;

        // straigt or dash line
        let dasharray = 0; if (aggregate == "fluid") { dasharray = 3 }

        // get DOM positions
        const f = from.getBoundingClientRect();
        const t = to.getBoundingClientRect();
        const p = parentDOM.getBoundingClientRect();

        // calc postiones of memories and activities anhängig vom Screen und Editor
        const p1 = { x: f.x/zoom-p.x/zoom, y: f.y/zoom-p.y/zoom, w: f.width/zoom, h: f.height/zoom};
        const p2 = { x: t.x/zoom-p.x/zoom, y: t.y/zoom-p.y/zoom, w: t.width/zoom, h: t.height/zoom};

        // calc coordinates, a is the rotation of the arrow spice
        let c, a, x, y, w;
        c = this.getDirectDistanceFromPoints(p1, p2, direction, from, to);
        if (direction == "input") { a = c.a; x = c.bX; y = c.bY; w = 180; }
        else { a = c.a; x = c.aX; y = c.aY; w = -180 }

        // PATH
        let path = document.createElementNS("http://www.w3.org/2000/svg", "path");
        path.setAttribute("d", `M${c.aX},${c.aY} L ${c.bX},${c.bY}`)
        path.setAttribute("fill", "none");
        path.setAttribute("stroke-width", "2");
        path.setAttribute("stroke-dasharray", dasharray);
        svg.appendChild(path);

        // SPICE
        let polygon = document.createElementNS("http://www.w3.org/2000/svg", "polygon");
        polygon.setAttribute("points", "0,-4 8,0, 0,4");
        polygon.setAttribute("transform", `translate(${x},${y}) rotate(${a})`);
        svg.appendChild(polygon);
    }

    // --------------------------------------------------------------------
    // 5. handle interactions
    // --------------------------------------------------------------------

    updateActiveDomElement(element) {

        let old = this.activeElement;

        // remove draggable from old active memory
        if (old) {
            if (old.classList.contains('memory')) {
                old.setAttribute('draggable', false);
                old.classList.remove("draggable");
            }
        }

        if (!element) { return };

        this.activeElement = element;

        // remove draggable from old active memory
        if (old) {
            if (old.classList.contains('memory')) {
               // old.classList.remove("draggable");
            }
        }
        // add draggable to new active memory
        if (element) {
            if (element.classList.contains('memory')) {
                element.setAttribute('draggable', true);
                element.classList.add("draggable");
            }
        }

        (old) ? this.removeActiveClass(old.id, old.dataset.category) : null;
        this.addActiveClass(element.id, element.dataset.category); 
    }

    removeActiveClass(id, category = null) {
        let oldDOM = document.getElementById(id);
        (oldDOM) ? oldDOM.classList.remove("active") : null;
        if (category) {
            let oldies = document.getElementsByClassName(category);
            for (let o of oldies) { o.classList.remove("activeSiblings"); }
        } 
    }

    addActiveClass(id, category = null) {
        let elementDOM = document.getElementById(id);
        elementDOM.classList.add("active");
        let newis = document.getElementsByClassName(category);
        for (let n of newis) { n.classList.add("activeSiblings"); }
    }

    // --------------------------------------------------------------------
    // 6. helper functions
    // --------------------------------------------------------------------

    mayBeMoved(parentId){
        let checker = false;
        let parentDOM = document.getElementById(parentId);
        if (parentDOM && this.activeElement) {
            if (this.activeElement.closest(".activity").id == parentDOM.closest(".activity").id) {
                checker = true;
            }
            if (parentDOM.parentNode.closest(".activity").id != this.activeElement.closest(".activity").id) {
                checker = false;
            }
        }
        return checker;
    }

    handleDropActivity(area, position, instruction) {

        let parent = area.parentNode;
        let parentSource = parent.getAttribute("data-source");
        let parentId; (parentSource == "static") ? parentId = parent.id :  parentId = area.closest('.static').id;
        let arrangement = parent.getAttribute("data-arrangement"); 
        let siblings = this.getStaticChildren(this.getChildren(parent));

        this.addActivity.trigger(instruction, parentId, parentSource, arrangement, siblings, position);
    }

    getChildren(node) {
        let children = [];
        if (node.hasChildNodes()) {
            let child = node.firstChild;
                while (child) {
                if (child.nodeType === 1 && child.nodeName != 'SCRIPT' && this.isClassOf(child, "container", "activity")) {
                    children.push(child);
                }
                child = child.nextSibling;
            }
        }
        return children;
    }

    getChildrenAndAreas(node) {
        let children = [];
        if (node.hasChildNodes()) {
            let child = node.firstChild;
                while (child) {
                if (child.nodeType === 1 && child.nodeName != 'SCRIPT' && this.isClassOf(child, "container", "activity", "area")) {
                    children.push(child);
                }
                child = child.nextSibling;
            }
        }
        return children;
    }

    isClassOf(node, ...classNames) {
        let result = false;
        classNames.forEach(name => {
            if (node.classList.contains(name)) { result = true };
        });
        return result;
    }

    isChildless(node) {
        let result = true;
        let children = this.getChildren(node);
        children.forEach( child => {
            if (this.isClassOf(child, "container", "activity")) { result = false; }
        });
        return result;
    }
    
    getAreasFromActivity(activity, areas = []) {
        this.getChildren(activity).forEach( child => {
            this.getAreaChildrenIds(activity).forEach( id => (!areas.includes(id)) ? areas.push(id) : null );
            this.getAreasFromActivity(child, areas);
        });
        return areas; 
    }

    getAreaChildrenIds(node) {
        let children = [];
        if (node.hasChildNodes()) {
            let child = node.firstChild;
            while (child) {
                if (child.nodeType === 1 && child.nodeName != 'SCRIPT' && this.isClassOf(child, "area")) {
                    children.push(child.id);
                }
                child = child.nextSibling
            }
        }
        return children;
    }

    getAreaFromNode(node, position, activity = null) {
            let pos = position * -1; 
            let children = [];
            if (node.hasChildNodes()) {
                let child = node.firstChild;
                while (child) {
                if (child.nodeType === 1 && child.nodeName != 'SCRIPT' && this.isClassOf(child, "area", "container", "activity")) {
                    children.push(child);
                }
                child = child.nextSibling
                }
            }
            if (children.length == 0) {
                return null;
            }

            let index = 0;
            let x = 0;

            let finder = children.map(function(c) { return c.id; }).indexOf(activity);
            if (finder > -1) {
                index = finder;
            }
            else {
                children.forEach( child => {
                    if(child.querySelector('#' + activity) != null) {
                        index = x;
                    }
                    x++;
                });
            }
            return children[index+pos];
    }

    nodeHasSiblings(node) {
        let result = false;
        const staticParent = node.parentNode.closest('.static');
        const siblings = this.getChildren(staticParent);
        if (siblings.length > 1) {
            result = true;
        }
        return result;
    }

    checkUnique(node, result = true, ids = []) {

        let children = this.getDynamicAndStaticChildren(node);
        if (ids.findIndex((i) => i == node.id) > -1) {
            result = false;
        }
        ids.push(node.id);
        children.forEach(child => {
            result = this.checkUnique(child, result, ids)
        });
        return result;
    }

    getDynamicAndStaticChildren(node) {
        let children = [];
        if (node.hasChildNodes()) {
            let child = node.firstChild;
                while (child) {
                if (child.nodeType === 1 && child.nodeName != 'SCRIPT' && this.isClassOf(child, "container", "activity", "area")) {
                    children.push(child);
                }
                child = child.nextSibling;
            }
        }
        return children;
    }

    updateActiveElement2(element = null) {
        if (element) {
            this.newActiveElement.trigger(element);
            this.removeActiveClass(element.id)
            this.addActiveClass(element.id);
        }
    }

    getStaticChildren(children, result = []) {
        children.forEach( child => {
            if (child.classList.contains("static")) {
                result.push(child.id);
            }
            else {
                this.getStaticChildren(this.getChildren(child), result);
            }
        });
        return result;
    }

    /** calculates the coordinates for the direct connection of a point and a rectangle
     *  @param {HTMLElement} obj1  First HTML element
     *  @param {HTMLElement} obj2  Secound HTML element
     *  @param {String} direction  direction of the relation. Input or output
     *  @returns {Object}
     */
    getDirectDistanceFromPoints(obj1, obj2, direction, from, to) {
        let p1, p2;
        let c = { aX: 0, aY: 0, bX: 0, bY: 0, d: 0, a: 0}; 

        // if direction == "output", then swap p1 and p2
        if (direction == "input") { p1 = obj1; p2 = obj2 }
        else { p1 = obj2; p2 = obj1 }

        let bMinX, bMaxX, bMinY, bMaxY, dX, dY;
        
        // Calculate center of aDOM
        c.aX = p1.x + (p1.w / 2);
        c.aY = p1.y + (p1.h / 2);

        // calculate shortest distance
        bMinX = p2.x;
        bMaxX = p2.x + p2.w;
        bMinY = p2.y;
        bMaxY = p2.y + p2.h;

        dX = Math.max(bMinX - c.aX, 0, c.aX - bMaxX);
        dY = Math.max(bMinY - c.aY, 0, c.aY - bMaxY);

        c.bX = c.aX;
        c.bY = c.aY;

        // move bx by
        let xLeft = bMinX - c.aX;
        let xRight = c.aX - bMaxX;
        
        let yTop = bMinY - c.aY;
        let yUnder = c.aY - bMaxY;
        
        if (xLeft > 0 && xLeft > xRight)          c.bX = bMinX; 
        else if ( xRight > 0 && xRight > xLeft)   c.bX = bMaxX;

        if (yTop > 0 && yTop > yUnder)            c.bY = bMinY; 
        else if ( yUnder > 0 && yUnder > yTop)    c.bY = bMaxY;

        c.d = Math.sqrt(dX * dX + dY * dY);

        // calculate angle for arrow and add space to activity
        /* 
        *   01 | 02 03 | 04     01-12 = position of memory
        *   12 |       | 05
        *   11 |       | 06
        *   10 | 09 08 | 07
        */

        let minX = c.aX - p1.w / 2;
        let maxX = c.aX + p1.w / 2;
        let minY = c.aY - p1.h / 2;
        let maxY = c.aY + p1.h / 2;

        
        // 01, 02, 03, 04 
        if (maxY <= p2.y) {
            // 01
            if (maxX <= p2.x) {
                if (direction == "input") { // clean
                    c.aX += 38; c.aY += 18; 
                    c.bX -= 11; c.bY -= 8;
                    c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                }
                else {
                    c.aX += 38; c.aY += 18; 
                    c.bX -= 11; c.bY -= 8;
                    c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                }
            }
            // 04
            else if (minX >= p2.x + p2.w) {
                if (direction == "input") { // clean
                    c.aX -= 38; c.aY += 10; 
                    c.bX += 2; c.bY -= 10;
                    c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                }
                else {  // clean
                    c.aX -= 44; c.aY += 15; 
                    c.bX -= 2; c.bY -= 0;
                    c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                }
            }
            // 02, 03
            else {
                // 02
                if (c.aX <= (p2.x + p2.w / 2)) {
                    if (direction == "input") {
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                        c.aX += 5; c.aY += 38; 
                        c.bX += 7; c.bY -= 12;
                    }
                    else {  //clean
                        c.aX += 5; c.aY += 44; 
                        c.bX += 7; c.bY -= 2;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                    }
                }
                // 03
                else {
                    if (direction == "input") { // clean
                        c.aX -= 5; c.aY += 38; 
                        c.bX -= 7; c.bY -= 12;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                    }
                    else {  // clean
                        c.aX -= 5; c.aY += 44; 
                        c.bX -= 7; c.bY -= 2;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                    }
                }
            }
        }
        // 07, 08, 09, 10
        else if (minY >= p2.y + p2.h) {
            // 10
            if (maxX <= p2.x) { //clean
                if (direction == "input") {
                    c.aX += 38; c.aY -= 18; 
                    c.bX -= 9; c.bY += 4;
                    c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                }
                else {
                    c.aX += 38; c.aY -= 18; 
                    c.bX -= 0; c.bY += 0;
                    c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                }
            }
            // 07
            else if (minX >= p2.x + p2.w) {
                if (direction == "input") {
                    c.aX -= 38; c.aY -= 18; 
                    c.bX += 8; c.bY += 8;
                    c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                }
                else {
                    c.aX -= 44; c.aY -= 18; 
                    c.bX -= 2; c.bY -= 2;
                    c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                }
            }
            // 08, 09
            else {
                // 09
                if (c.aX <= (p2.x + p2.w / 2)) {
                    if (direction == "input") { // clean
                        c.aX += 5; c.aY -= 38;
                        c.bX += 7; c.bY += 10;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                    }
                    else {  //clean
                        c.aX += 5; c.aY -= 44; 
                        c.bX += 7; c.bY += 0;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                    }
                }
                // 08
                else {
                    if (direction == "input") { //clean
                        c.aX -= 5; c.aY -= 38; 
                        c.bX -= 7; c.bY += 8;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                    }
                    else { // clean
                        c.aX -= 5; c.aY -= 44; 
                        c.bX -= 7; c.bY += 0;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                    }
                }
            }
        }
        // 05, 06, 11, 12
        else {
            // 11, 12
            if (maxX <= p2.x) {
                // 12
                if (c.aY <= (p2.y + p2.h / 2)) {
                    c.aX += 38; c.aY += 5; 
                    c.bX -= 10; c.bY += 5;
                    if (direction == "input") {
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                    }
                    else {
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                    }
                }
                // 11
                else {
                    if (direction == "input") {
                        c.aX += 38; c.aY -= 5; 
                        c.bX -= 10; c.bY -= 5;
                        if (direction == "input") {
                            c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                        }
                        else {
                            c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                        }
                    }
                }
            }
            // 05, 06
            else if (minX >= p2.x + p2.w) {
                // 05
                if (c.aY <= (p2.y + p2.h / 2)) {
                    if (direction == "input") { //clean
                        c.aX -= 38; c.aY += 5; 
                        c.bX += 8; c.bY += 5;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;
                    }
                    else {
                        c.aX -= 44; c.aY += 5; 
                        c.bX -= 2; c.bY += 5;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                    }
                }
                // 06
                else {
                    if (direction == "input") { // clean
                        c.aX -= 45; c.aY -= 5; 
                        c.bX += 0; c.bY -= 5;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI) - 180;;
                    }
                    else {
                        c.aX -= 45; c.aY -= 5; 
                        c.bX += 0; c.bY -= 5;
                        c.a = Math.atan( (c.bY - c.aY) / (c.bX-c.aX) ) * (180 / Math.PI);
                    }
                }
            }
            else {
                console.log(from, to);
            }
        }

     return c;
    }
}

export default ProcessView;