import './information.css'
import View from "../../View";
import EventObserver from "../../EventObserver";
import Memory from "../process/processModules/Memory";
import { ActivityNode } from '../process/processModules/Node';
import deleteIcon from '../../../svg/delete.svg';

/**
 * Creates an instance of a sidebar
 *  @param {Process} process
 *  @param {Object} contextFactors
 */
class informationView extends View {
    constructor(process, contextFactors) {
        super();
        this._element = null;  // null || ActivityNode || MemoryNode
        this._process = process;
        this._contextFactors = contextFactors;

        // Process
        this.changeProcessName = new EventObserver();
        this.changeContextFactors = new EventObserver();
        this.exportProcess = new EventObserver();
        this.importProcess = new EventObserver();

        // Activties & Memories
        this.changeElementName = new EventObserver(); 
        this.importProcess = new EventObserver();
        this.exportProcess = new EventObserver();
        this.deleteElement = new EventObserver();
        this.deleteElementModalAction = new EventObserver();
        this.changeDetailLevel = new EventObserver();
        this.changeColor = new EventObserver();
        this.changeDuration = new EventObserver();
        this.addTask = new EventObserver();
        this.addInfo = new EventObserver();
        this.removeTask = new EventObserver();
        this.removeInfo = new EventObserver();
        this.makeDecision = new EventObserver();
        this.submitContextFactors = new EventObserver();
    }

    get element() {
        return this._element;
    }

    set element(element) {
        this._element = element;
    }
    
    get process() {
        return this._process;
    }

    get contextFactors() {
        return this._contextFactors;
    }

    updateActiveElement(element) {
        this.element = element;
    }

    /**
     * Creates the HTML element of the sidebar
     */
    render() {
        const information = this.root;
        information.id = "information";
        this.parentDOM.appendChild(information);

        // Process Settings: Process Name, Edit Context Factors, Import & Export Process
        const process = this.createElement("div", null, ["process", "settingsContainer"]);
        information.appendChild(process);

            // Process Name
            const processNameInput = this.createElement("input", null, ["name"]);
            processNameInput.value = this.process.name;
            processNameInput.setAttribute("type", "text");
            processNameInput.setAttribute("maxlength", "30");
            process.appendChild(processNameInput);
            processNameInput.addEventListener('focusout', () => {
                if (processNameInput.value != this.process.name) {
                    this.changeProcessName.trigger(processNameInput.value);
                }
            });
            
            const processNameInputLabel = this.createElement("div", null, ["json"]);
            processNameInputLabel.textContent = ".json"
            process.appendChild(processNameInputLabel);
            
            // Buttons
            const btnContainer = this.createElement("div", null, ["btnContainer"]);
            information.appendChild(btnContainer);
                
                // Context Factors
                const contextFactorsBtn = this.createElement("div", null, ["processBtn"]);
                contextFactorsBtn.textContent = "Edit Context Factors"
                btnContainer.appendChild(contextFactorsBtn);
                contextFactorsBtn.addEventListener('click', () => {
                    this.changeContextFactors.trigger();
                });


                // Import Process
                const fileLoader = this.getFileLoader(this.importProcess);
                btnContainer.appendChild(fileLoader);

                const importBtn = this.createElement("div", null, ["processBtn"]);
                importBtn.textContent = "Import"
                btnContainer.appendChild(importBtn);
                importBtn.addEventListener('click', () => {
                    document.getElementById("fileLoader").click();
                });

                // Export Process
                const exportBtn = this.createElement("div", null, ["processBtn"]);
                exportBtn.textContent = "Export"
                btnContainer.appendChild(exportBtn);
                exportBtn.addEventListener('click', () => {
                    this.exportProcess.trigger();
                });

        // Render Activity or Memory Element
        if (this.element != null) {

            // Icon & Element Name
            let icon = this.getIcon(this.element); 
            
            const img = this.createElement("img", null, ["elementIcon"]);
            img.setAttribute("src", icon);
            information.appendChild(img);
            if (this.element instanceof ActivityNode) {
                (this.element.color != "#ffffff") ? img.style.borderColor = this.element.color : img.style.borderColor = "black";
            } 
            else { img.style.borderColor = "black"; }
                
            
            // Element Settings
            const settings = this.createElement("div", null, ["settingsContainer"]);
            information.appendChild(settings);
            
            const elementNameInput = this.createElement("input", null, ["name"]);
            elementNameInput.value = this.element.name;
            elementNameInput.setAttribute("type", "text");
            elementNameInput.setAttribute("maxlength", "30");
            settings.appendChild(elementNameInput);
            elementNameInput.addEventListener('focusout', () => {
                if (elementNameInput.value != this.element.name) {
                    this.changeElementName.trigger(this.element, elementNameInput.value);
                }
            });

            // Delete Element Button
            const deleteElement = this.createElement("img", null, ["deleteBtn"]);
            deleteElement.setAttribute("src", deleteIcon);
            settings.appendChild(deleteElement);
            deleteElement.addEventListener('click', () => {
                this.deleteElement.trigger(this.element);
            });
            
            // Render Memory or Activity Details
            let details;
            if (this.element instanceof Memory) {
                details = this.createMemoryDetails(this.element);
            } 
            else if (this.element instanceof ActivityNode) {
                details = this.createActivityDetails(this.element);
                
            }
            information.append(details);
        }
            return sidebar;
    }

    /**
     * Shows the meta information of a memory
     *
     * @param {Memory} memory Memory to be displayed
     */
    createMemoryDetails(memory) {
            const memoryDetails = document.createElement("div");
            memoryDetails.classList.add("memoryDetails");
            return memoryDetails;
    }

    /**
     * Shows the meta information of a activity
     *
     * @param {Activity} activity Activity to be displayed
     */
    createActivityDetails(activity) {

        // Wrapper
        const activityDetails = this.createElement("div", null, ["activityDetails"]);

            // Level of Detail
            const level = this.createElement("div", null, ["level", "container"]);
            activityDetails.appendChild(level);

                const levelTitle = this.createElement("span", null, ["title"]);
                levelTitle.textContent = "Level of Detail";
                level.appendChild(levelTitle);

                const levelScaleContainer = this.createElement("div", null, ["scaleContainer"]);
                level.appendChild(levelScaleContainer);

                    let levelScales = [];
                    for (let i = 0; i < 3; i++) {
                        levelScales.push(this.createElement("div", null, ["scale"]));
                    }
                    for (let i = 1; i < 4; i++) {
                        levelScales[i-1].innerHTML = '<span>' + i + '</span>';
                        levelScales[i-1].value = i;
                        (activity.levelOfDetail == i) ? levelScales[i-1].classList.add("active") : null;
                        levelScales[i-1].addEventListener('click', (event) => {
                            this.changeDetailLevel.trigger(activity, event.target.value);
                        });
                        levelScales[i-1].style.background = this.getColor(activity.color, i )
                    }
                    
                    const colorInput = this.createElement("input", null, ["color"]);
                    colorInput.setAttribute("type", "color");
                    colorInput.value = activity.color;
                    colorInput.addEventListener('change', () => {
                        this.changeColor.trigger(activity, colorInput.value);
                        for(let i = 0; i < levelScales.length; i++) { levelScales[i].style.background = this.getColor(colorInput.value, i ); }
                    })
                    
                    levelScaleContainer.appendChild(colorInput);
                    levelScales.forEach(level => levelScaleContainer.appendChild(level));

            // Duration
            const duration = this.createElement("div", null, ["duration", "container"]);
            activityDetails.appendChild(duration);

                const durationTitle = this.createElement("span", null, ["title"]);
                durationTitle.textContent = "Duration";
                duration.appendChild(durationTitle);

                const durationScaleContainer = this.createElement("div", null, ["scaleContainer"]);
                duration.appendChild(durationScaleContainer);
        
                    const durations = ["S", "M", "L"]
                    const durationsLabel = ["short", "middle", "long"]

                    for (let i = 0; i < 3; i++) {
                        const durationScale = this.createElement("div", null, ["scale"]);
                        durationScale.innerHTML = '<span>' + durations[i]; + '</span>';
                        durationScale.value = i + 1;
                        durationScaleContainer.appendChild(durationScale);
                        if (activity.duration == i + 1 || activity.duration == durationsLabel[i]) {
                            durationScale.classList.add("active");
                        }
                        durationScale.addEventListener('click', (event) => {
                            this.changeDuration.trigger(activity, event.target.value);
                        });
                    }

            // Information
            const info = this.createElement("div", null, ["info", "container"]);
            activityDetails.appendChild(info);

                const infoTitle = this.createElement("span", null, ["title"]);
                infoTitle.textContent = "Information";
                info.appendChild(infoTitle);

                if (activity.infos.length >= 1) {
                    activity.infos.forEach( i => {
                        const infoItem = this.createElement("div", null, ["item"]);
                        infoItem.textContent = i.text;
                        info.appendChild(infoItem);

                        const infoDelete = this.createElement("img", null, ["deleteBtn", "deletePos"]);
                        infoDelete.setAttribute("src", deleteIcon);
                        infoItem.appendChild(infoDelete);
                        infoDelete.addEventListener('click', () => {
                            console.log(infoItem.textContent);
                        this.removeInfo.trigger(activity, infoItem.textContent);
                        })
                    });

                }
                else {
                    const infoItem = this.createElement("span", null, ["noItem"]);
                    infoItem.textContent = "No information for this activity"
                    info.appendChild(infoItem);
                }

                // Add Info
                const infoInputContainer = this.createElement("div", null, ["inputContainer"]);
                info.appendChild(infoInputContainer);

                const infoInput = this.createElement("input", null, ["input"]);
                infoInput.setAttribute("maxlength", "140");
                infoInputContainer.appendChild(infoInput);

                const infoAdd = this.createElement("div", null, ["addBtn"]);
                infoAdd.textContent = "Add";
                infoInputContainer.appendChild(infoAdd);
                infoAdd.addEventListener('click', () => {
                    if (infoInput.value != "") {
                        this.addInfo.trigger(activity, infoInput.value);
                    }
                })


            // Tasks
            const tasks = this.createElement("div", null, ["tasks", "container"]);
            activityDetails.appendChild(tasks);

                const tasksTitle = this.createElement("span", null, ["title"]);
                tasksTitle.textContent = "Tasks";
                tasks.appendChild(tasksTitle);

                if (activity.tasks.length >= 1) {
                    activity.tasks.forEach( i => {
                        const tasksItem = this.createElement("span", null, ["item"]);
                        tasksItem.textContent = i.text;
                        tasks.appendChild(tasksItem);

                        const taskDelete = this.createElement("img", null, ["deleteBtn", "deletePos"]);
                        taskDelete.setAttribute("src", deleteIcon);
                        tasksItem.appendChild(taskDelete);
                        taskDelete.addEventListener('click', () => {
                            console.log(tasksItem.textContent);
                            this.removeTask.trigger(activity, tasksItem.textContent);
                        })
                    });
                }
                else {
                    const tasksItem = this.createElement("span", null, ["noItem"]);
                    tasksItem.textContent = "No tasks for this activity"
                    tasks.appendChild(tasksItem);
                }

                // Add Task
                const taskInputContainer = this.createElement("div", null, ["inputContainer"]);
                tasks.appendChild(taskInputContainer);

                const taskInput = this.createElement("input", null, ["input"]);
                taskInput.setAttribute("maxlength", "140");
                taskInputContainer.appendChild(taskInput);

                const taskAdd = this.createElement("div", null, ["addBtn"]);
                taskAdd.textContent = "Add";
                taskInputContainer.appendChild(taskAdd);
                taskAdd.addEventListener('click', () => {
                    if (taskInput.value != "") {
                        this.addTask.trigger(activity, taskInput.value);
                    }
                })

        // Context Factors 
        let phaseInputs = this.contextFactors.phaseInputs;
        const decisionValues = this.contextFactors.decisionValues;

        const contextFactors = this.createElement("div", null, ["container"]);
        activityDetails.appendChild(contextFactors);

            // Get Context Factores From Phase Inputs And Render Quiz
            let counter = 0;
            phaseInputs.forEach(input => {
                if (input.phase == activity.id || input.phase.includes(activity.id)) {
                    counter++;
                    
                    const contextFactorTitle = this.createElement("span", null, ["titleSmall"]);
                    contextFactorTitle.textContent = input.context;
                    contextFactors.appendChild(contextFactorTitle);

                    const optionForm = document.createElement("form");
                    contextFactors.appendChild(optionForm);

                    for (let i = 0; i < input.options.length; i++) {
                        
                        const optionContainer = document.createElement("div");
                        optionForm.appendChild(optionContainer);
                        
                        const optionInput = document.createElement("input");
                        optionInput.setAttribute("type", "radio");
                        optionInput.setAttribute("name", input.id);
                        optionInput.setAttribute("id", input.id + i);
                        optionInput.setAttribute("value", i);
                        optionContainer.appendChild(optionInput);
                        (decisionValues.get(input.id) == i) ? optionInput.checked = true : null;
                        
                        optionInput.addEventListener('change', () => {
                            console.log(this.contextFactors.decisionValues);
                            this.makeDecision.trigger(input.id, optionInput.value);
                            console.log(this.contextFactors.decisionValues);
                        });
                        
                        const optionLabel = document.createElement("label");
                        optionLabel.textContent = input.options[i];
                        optionLabel.setAttribute("for", input.id + i);
                        optionContainer.appendChild(optionLabel);
                    }
                }
            });

            if (counter >= 1) {
                const submitBtn = document.createElement("button");
                contextFactors.appendChild(submitBtn);
                submitBtn.textContent = "Update Process";
                submitBtn.addEventListener('click', (event) => {
                    this.submitContextFactors.trigger();
                });
            }

        return activityDetails;
    }

    /**
     * Shows a modal window to delete an element
     *
     * @param {Node} parentDOM Parent node of the element
     * @param {Object} element Object to be deleted: Activity or Node
     */
    deleteElementModal(parentDOM, element) {
        
        let title = "";

        const message = document.createElement("p");
        if (element instanceof ActivityNode) {
            message.textContent = "You want to delete an activity. All subordinate activities and memories will also be deleted. Do you want to continue?";
            title = "Delete Activity";
        }
        else {
            message.textContent = "Do you want to delete this memory?";
            title = "Delete Memory";
        }

        const cancelBtn = document.createElement("img");
        cancelBtn.addEventListener('click', () => {
            this.deleteElementModalAction.trigger();
        });
        
        const submitBtn = document.createElement("button");
        submitBtn.addEventListener('click', () => {
            this.deleteElementModalAction.trigger(element);
        });

        let modal = this.createModal(title, element, message, cancelBtn, submitBtn);
        parentDOM.appendChild(modal);
    }

}

export default informationView;