class Modification {

    constructor(source = "system", text = "") {
        this._source = source;
        this._text = text;
    }

    get source() {
        return this._source;
    }
    
    set source(source){
        this._source = source;
    }

    get text() {
        return this._text;
    }
    
    set text(text){
        this._text = text;
    }
}

export default Modification;