class Zoom {

    /**
     * Creates a zoom instance that can zoom in and out in the variable zoomTarget HTML element.
     */
    constructor() {
        this._zoomValue = 10;
        this._zoomTarget = null;
    }

    get zoomValue() {
        return this._zoomValue;
    }

    set zoomValue(value) {
        this._zoomValue = value;
    }

    get zoomTarget() {
        return this._zoomTarget;
    }

    set zoomTarget(value) {
        this._zoomTarget = value;
    }

    /**
     * Initializes the zoom instance by specifying an HTML element as zoom target.
     *
     * @param {HTMLElement} zoomTarget The HTML element to be zoomed in and out.
     */
    initZoom(zoomTarget) {
        if (zoomTarget) {
            this.zoomFull(zoomTarget);
        }
    }

    /**
     * Zoom into zoomTarget.
     */
    zoomIn() {
        this.zoomValue++;
        this.showVal(this.zoomValue, this.zoomTarget);
    }

    /**
     * Zoom out from zoomTarget
     */
    zoomOut() {
        this.zoomValue--;
        this.showVal(this.zoomValue, this.zoomTarget);
    }

    /**
     * controller for the zoom action
     *
     * @param {Number} Zoom level of zoom action
     * @param {HTMLElement} zoomTarget Html element that should be enlarged or reduced in size
     */
    showVal(zoomValue, zoomTarget){
        var zoomScale = Number(zoomValue)/10;
        this.setZoom(zoomScale, zoomTarget);
    }

    /**
     * Executes the zoom action
     *
     * @param {Number} Zoom level of zoom action
     * @param {HTMLElement} zoomTarget Html element that should be enlarged or reduced in size
     */
    setZoom(zoomScale, zoomTarget) {
        let transformOrigin = [0,0];
            zoomTarget = zoomTarget || instance.getContainer();
            var p = ["webkit", "moz", "ms", "o"],
                s = "scale(" + zoomScale + ")",
                oString = (transformOrigin[0] * 100) + "% " + (transformOrigin[1] * 100) + "%";

            for (var i = 0; i < p.length; i++) {
                zoomTarget.style[p[i] + "Transform"] = s;
                zoomTarget.style[p[i] + "TransformOrigin"] = oString;
            }

            zoomTarget.style["transform"] = s;
            zoomTarget.style["transformOrigin"] = oString;
    }

}

export default Zoom;