import Modification from "./Modification";

class Node {

    constructor(id) {
        this._id = id;
        this._children = [];
    }

    get id() {
        return this._id;
    }
    
    set id(id) {
        this._id = id;
    }

    get children() {
        return this._children;
    }
    
    set children(children){
        this._children = children;
    }

    nodeSerialize() {
        return undefined;
    }
    
    childrenSerialize() {
        let serialized = [];
        this.children.forEach( child => {
            serialized.push(child.nodeSerialize());
        });
        return serialized;
    }
}

class ContainerNode extends Node {
    
    constructor(id, arrangement){
        super(id);
        this._arrangement = arrangement;
    }
    
    get arrangement(){
        return this._arrangement;
    }
    
    set arrangement(arrangement){
        this._arrangement = ["parallel","sequential"].indexOf(arrangement) >= 0 ? arrangement : "parallel";
    }
    
    nodeSerialize() {
        return {type: "container", id: this.id, arrangement: this.arrangement, children: this.childrenSerialize()};
    }

}

class ActivityNode extends Node {

    constructor(id, name, aggregate = "solid", phase = "", duration = 2, levelOfDetail = 0, tasks = [], infos = [], color = "#ffffff") {
        super(id);
        this._name = name;
        this._aggregate = aggregate;
        this._phase = phase;
        this._duration =  duration;
        this._levelOfDetail = levelOfDetail;
        this._color = color;
        this._tasks = tasks;
        this._infos = infos;
    }

    get name() {
        return this._name;
    }
    
    set name(name) {
        this._name = name;
    }

    get aggregate() {
        return this._aggregate;
    }

    set aggregate(aggregate) {
        this._aggregate = aggregate;
    }

    get phase() {
        return this._phase;
    }

    set phase(phase) {
        this._phase = phase;
    }

    get duration() {
        return this._duration;
    }
    
    set duration(d) {
        this._duration = d;
    }

    get levelOfDetail() {
        return this._levelOfDetail;
    }

    set levelOfDetail(level) {
        this._levelOfDetail = level;
    }

    get tasks() {
        return this._tasks;
    }

    set tasks(tasks) {
        this._tasks = tasks;
    }

    get infos() {
        return this._infos;
    }

    set infos(infos) {
        this._infos = infos;
    }

    get dragStatus() { 
        return this._dragStatus;
    }

    set dragStatus(status) {
        this._dragStatus = status;
    }

    get color () {
        return this._color;
    }

    set color(color) {
        this._color = color;
    }

    nodeSerialize() {
        return {type: "activity", id: this.id, name: this.name, aggregate: this.aggregate, phase: this.phase, duration: this.duration, levelOfDetail: this.levelOfDetail, color: this.color, tasks: this.taskSerialize(), infos: this.infosSerialize(), children: this.childrenSerialize()};
    }

    taskSerialize() {
        let tasks = [];
        this.tasks.forEach(task => {
            tasks.push(task.text);
        });
        return tasks;
    }

    infosSerialize() {
        let infos = [];
        this.infos.forEach(info => {
            infos.push(info.text);
        });
        return infos;
    }

    setDuration(d) {
        let result = d;
        if      (d == "none")   { result = 0 }
        else if (d == "short")  { result = 1 }
        else if (d == "middle") { result = 2 }
        else if (d == "long")   { result = 3 }
        this.duration = result;
    }
    
    addTask(source, text) {
        if (this.tasks.findIndex(task => task.text == text) == -1) {
            const task = new Modification(source, text);
            this.tasks.push(task);
        }
    }

    addTasks(tasks, source) {
        tasks.forEach((task) => {
            this.addTask( source, task);
        });
        
    }
    
    removeTask(text) {
        this.tasks = this.tasks.filter( t => ( t.text != text ));
    }

    addInfo(source, text) {
        if (this.infos.findIndex(info => info.text == text) == -1) {
            const info = new Modification(source, text);
            this.infos.push(info);
        }
    }

    addInfos(infos, source) {
        infos.forEach((info) => {
            this.addInfo( source, info);
        });
        
    }
    
    removeInfo(text) {
        this.infos = this.infos.filter( i => ( i.text != text ));
    }
}

export {ContainerNode, ActivityNode};