import StartScreenView from './modules/startScreen/StartScreenView';

import ContextFactors from './modules/contextFactors/ContextFactors';
import ContextFactorsView from './modules/contextFactors/ContextFactorsView';

import EditorScreen from './modules/editorScreen/EditorScreen';
import EditorScreenView from './modules/editorScreen/EditorScreenView';

import controlPanelView from './modules/controlPanel/ControlPanelView';

import Zoom from './modules/zoom/Zoom';
import ZoomView from './modules/zoom/ZoomView';

import Process from './modules/process/Process';
import ProcessView from './modules/process/ProcessView';

import InformationView from './modules/information/InformationView.js';

class Controller {

    constructor() {

        // ------------------------------------------
        // load and create Modules, Views and Handler
        // ------------------------------------------

        // START SCREEN
        this.startScreenView = new StartScreenView();
        this.startScreenView.clickImport.addHandler((file) => this.handleImportProcess(file));
        this.startScreenView.clickCreateNew.addHandler((parentDOM) => this.handleClickCreateNewProcess(parentDOM));

        // CONTEXT FACTORES
        this.contextFactors = new ContextFactors();
        this.contextFactorsView = new ContextFactorsView(this.contextFactors);
        this.contextFactorsView.closeContextFactors.addHandler((parentDOM) => this.handleCloseContextFactors(parentDOM));
        this.contextFactorsView.nextContextFactor.addHandler((parentDOM) => this.handleNextContextFactor(parentDOM));
        this.contextFactorsView.lastContextFactor.addHandler((parentDOM) => this.handleLastContextFactor(parentDOM));
        this.contextFactorsView.makeDecision.addHandler((key, value) => this.handleMakeDecision(key, value));
        this.contextFactorsView.submitContextFactors.addHandler((parentDOM) => this.handleSubmitContextFactors(parentDOM));

        // EDITOR SCREEN
        this.editorScreen = new EditorScreen();
            this.editorScreen.setMoving.addHandler((value) => this.handleSetMoving(value));
        this.editorScreenView = new EditorScreenView(this.editorScreen);
        this.editorScreenView.stopMoveScreen.addHandler(() => this.handleStopMoveScreen());
        this.editorScreenView.clicked.addHandler((target) => this.handleClicked(target));

        // CONTROL PANEL
        this.controlPanelView = new controlPanelView();
        this.controlPanelView.passDragObject.addHandler((instruction) => this.handlePassDragObject(instruction));
        this.controlPanelView.addActivityModalAction.addHandler((activity = null, parentId = null, parentSource = null, arrangement = null, siblings = null, position = null, create = false) => this.handleAddActivityModalAction(activity, parentId, parentSource, arrangement, siblings, position, create));
        this.controlPanelView.addMemoryModalAction.addHandler((memory = null, parent = null, create = false) => this.handleAddMemoryModalAction(memory, parent, create))
        
        // Zoom
        this.zoom = new Zoom();
        this.zoomView = new ZoomView();
            this.zoomView.zoomIn.addHandler((zoom) => this.handleZoomIn(zoom));
            this.zoomView.zoomOut.addHandler((zoom) => this.handleZoomOut(zoom));
            this.zoomView.initPosition.addHandler(() => this.handleInitPosition());
            this.zoomView.oldPosition.addHandler(() => this.handleOldPosition());

        // PROCESS
        this.process = new Process("new Process");
        this.processView = new ProcessView(this.process, this.zoom, this.editorScreen);

            this.processView.removeMemory.addHandler((memory) => this.handleRemoveMemory(memory));
            this.processView.shiftMemory.addHandler((memory, oldId, newId, parent, relationId, relationType) => this.handleShiftMemory(memory, oldId, newId, parent, relationId, relationType));
            this.processView.newActiveElement.addHandler((element) => this.handleNewActiveElement(element));
            this.processView.passDragObject.addHandler((obj) => this.handlePassDragObject(obj));
            this.processView.addExistingMemory.addHandler((id, direction, parent) => this.handleAddExistingMemory(id, direction, parent));
            this.processView.relocatingMemory.addHandler((area, parentId) => this.handleRelocatingMemory(area, parentId));

            this.processView.addMemory.addHandler((instruction, id, direction, parent) => this.handelAddMemory(instruction, id, direction, parent));
            this.processView.addActivity.addHandler((instruction, parentId, parentSource, arrangement, siblings, position) => {
                this.handleAddActivity(instruction, parentId, parentSource, arrangement, siblings, position)
            });

        // InformationView / Sidebar
        this.informationView = new InformationView(this.process, this.contextFactors);
            // Process
            this.informationView.changeProcessName.addHandler((name) => this.handleChangeProcessName(name));
            this.informationView.changeContextFactors.addHandler((factors) => this.handleChangeContextFactors(factors));
            this.informationView.importProcess.addHandler((file) => this.handleImportProcess(file));
            this.informationView.exportProcess.addHandler(() => this.handleExportProcess());
            // Element
            this.informationView.changeElementName.addHandler((element, value) => this.handleChangeElementName(element, value));
            this.informationView.deleteElement.addHandler((element) => this.handleDeleteElement(element));
            this.informationView.deleteElementModalAction.addHandler((element = null) => this.handleDeleteElementModalAction(element));
            this.informationView.changeColor.addHandler((activity, value) => this.handleChangeColor(activity, value));
            this.informationView.changeDetailLevel.addHandler((activity, value) => this.handleChangeDetailLevel(activity, value));
            this.informationView.changeDuration.addHandler((activity, value) => this.handleChangeDuration(activity, value));
            this.informationView.addTask.addHandler((activity, value) => this.handleAddTask(activity, value));
            this.informationView.addInfo.addHandler((activity, value) => this.handleAddInfo(activity, value));
            this.informationView.removeTask.addHandler((activity, value) => this.handleRemoveTask(activity, value));
            this.informationView.removeInfo.addHandler((activity, value) => this.handleRemoveInfo(activity, value));
            this.informationView.makeDecision.addHandler((key, value) => this.handleMakeDecision(key, value))
            this.informationView.submitContextFactors.addHandler(() => this.handleSubmitContextFactors());

        // ------------------------------------------
        // get HTML areas from editor
        // ------------------------------------------

        this.sidebar = document.getElementById("sidebar");
        this.content = document.getElementById("content");
        this.dragScroll = null;
    }

// CONTROLL WHAT TO RENDER IN APP -----------------------

    run() {
        this.showStartScreen(this.content);

        // Abkürzung
        //this.handleClickCreateNewProcess(this.content);
        //this.handleSubmitContextFactors(this.content);
    }
    
    showStartScreen(parentDOM) {
        this.startScreenView.visible = true;
        this.contextFactorsView.visible = false;
        this.editorScreenView.visible = false;
        this.processView.visible = false;
        this.zoomView.visible = false;
        this.controlPanelView.visible = false;
        this.editorScreenView.visible = false;
        this.informationView.visible = false;

        this.contextFactors.decisionPointer = 1;
        this.updateContent(parentDOM);
    }

    showProcess(parentDOM) {
        this.startScreenView.visible = false;
        this.contextFactorsView.visible = false;
        this.editorScreenView.visible = true;
        this.processView.visible = true;
        this.zoomView.visible = true;
        this.controlPanelView.visible = true;

        this.contextFactors.decisionPointer = 1;
        this.updateContent(parentDOM);
        this.updateSidebar();
    }

    updateSidebar() {
        this.sidebar.innerHTML = "";
        this.informationView.toggle(this.sidebar);
    }

    updateContent() {
        this.informationView.visible = true;

        if (this.editorScreen.screen) { 
            this.editorScreen.anchor();
        };

        this.content.innerHTML = "";
        this.process.idCounter = 0;

        // If the visible variable of the views is equal to true, the single views are displayed
        this.startScreenView.toggle(this.content); 
        this.contextFactorsView.toggle(this.content);
        let editorScreen = this.editorScreenView.toggle(this.content);
        this.editorScreen.startEditorScreen(editorScreen);
        const editor = this.processView.toggle(editorScreen);
        this.editorScreen.processScreen = editor;
        this.zoom.zoomTarget = editor;
        this.zoomView.toggle(this.content, this.zoom);
        this.controlPanelView.toggle(this.content);

        this.editorScreen.setPosition();
        this.dragScroll = document.getElementById("dragScrollContainer");
    }

    // ----------------------------------------
    // Import and Export 
    // ----------------------------------------

    handleImportProcess(file) {

        if (!file.size > 0) return;

        let reader = new FileReader();
        reader.readAsText(file);

        reader.addEventListener('load', (event) => {
            let str = event.target.result;
            let process = JSON.parse(str);
            if (this.process.setInitialProcess(process)) {
                this.showProcess(this.content);
            }
        })
    }

    handleExportProcess() {
        this.process.export(this.contextFactors.contextFactorsSerialize());
    }

    handleClickCreateNewProcess() {
        this.startScreenView.visible = false;
        this.contextFactorsView.visible = true;
        this.updateContent(this.content);
    }

    // ----------------------------------------
    // Context Facors
    // ----------------------------------------

    handleCloseContextFactors() {
        this.showStartScreen(this.content);
    }

    handleNextContextFactor() {
        this.contextFactors.decisionPointer++;
        this.updateContent(this.content);
    }

    handleLastContextFactor() {
        this.contextFactors.decisionPointer--;
        this.updateContent(this.content);
    }

    handleMakeDecision(key,value) {
        this.contextFactors.updateDecisionValues(key, value);
    }

    handleSubmitContextFactors(parentDOM) { 
        this.process.setInitialProcess();
        this.contextFactors.makeDecisions();
        this.process.modifyProcess(this.contextFactors.modifications);

        this.contextFactors.decisionPointer = 1;
        this.contextFactorsView.visible = false;
        this.editorScreenView.visible = true;
        this.processView.visible = true;
        this.zoomView.visible = true;
        this.controlPanelView.visible = true;
        this.updateContent(this.content);
        this.updateSidebar();
    }

    // ----------------------------------------
    // MOVE SCREEN
    // ----------------------------------------

    handleMoveScreen(screen, x, y) {
        this.editorScreen.mouseDownHandler(screen, x, y);
    }

    handleStopMoveScreen() {
        this.editorScreen.control = false;
    }

    // ----------------------------------------
    // Add Elements
    // ----------------------------------------

    handleAddActivity(instruction, parentId, parentSource, arrangement, siblings, position) {
        let activity = this.process.createActivity(instruction);
        this.controlPanelView.addActivityModal(activity, parentId, parentSource, arrangement, siblings, position);
    }

    handleAddActivityModalAction(activity, parentId, parentSource, arrangement, siblings, position, create) {
        if(create) {
            this.process.addActivity(activity, parentId, parentSource, arrangement, siblings, position);
            this.handleNewActiveElement(activity);
        }
        this.updateContent(this.content);
    }

    handelAddMemory(instruction, id, direction, parent) {
        this.zoom.initZoom();
        let memory = this.process.createMemory(instruction, id, direction, parent);
        this.controlPanelView.addMemoryModal(memory, parent);
        this.process.dragAction = null;
    }

    handleAddMemoryModalAction(memory, parent, create) {
        if(create) {
            this.process.addToArea("root", memory, parent);
            this.handleNewActiveElement(memory);
        }
        this.updateContent(this.content);
    }

    // ----------------------------------------
    // ProcessView
    // ----------------------------------------

    handleRemoveMemory(memory) {
        this.process.removeMemory(memory);
    }

    handleShiftMemory(memory, oldId, newId, parent, relationId, relationType) {
        this.process.shiftMemory(memory, oldId, newId, parent, relationId, relationType);
    }

    // DOM ELEMENT
    handleClicked(target) {  
        this.editorScreen.updateClickedTarget(target);
        this.controlPanelView.updateDragScroll(this.dragScroll, target);
        this.processView.updateActiveDomElement(target);
        this.editorScreenView.updateCursor(target);
    }

    // NODE ELEMENT
    handleNewActiveElement(element) {
        this.process.updateActiveElement(element);
        this.informationView.updateActiveElement(element);
        this.updateSidebar();
    }

    handleSetMoving(value) {
        this.editorScreenView.setMoving(value);
    }

    handlePassDragObject(obj) {
        this.process.dragObject = obj;
        this.editorScreenView.dragAction(obj);
        if (obj.data == null) {
            this.processView.updateActiveDomElement(null);
        }
    }

    handleAddExistingMemory(id, direction, parent) {
        let newMemory = this.process.duplicateMemory(id, direction);
        this.process.addToArea("root", newMemory, parent);
        this.processView.dragObject = null;
        this.updateContent(this.content);
    }

    handleRelocatingMemory(area, parentId) {
        let newMemory = this.process.duplicateActiveMemory();
        this.process.deleteElement(this.process.activeElement);
        this.process.addToArea(area.id, newMemory, parentId)
        this.updateContent(this.content);
        this.updateSidebar();
    }

    // ----------------------------------------
    // Zoom
    // ----------------------------------------

    handleZoomIn(zoom) {
        this.zoom.zoomIn(zoom);
    }

    handleZoomOut(zoom) {
        this.zoom.zoomOut(zoom);
    }

    handleInitPosition() {
        this.editorScreen.initPosition();
    }

    handleOldPosition() {
        this.editorScreen.setPosition();
    }

    // ----------------------------------------
    // InformationView / Sidebar Actions
    // ----------------------------------------

    handleChangeProcessName(name) {
        this.process.changeProcessName(name);
        this.updateSidebar();
    }

    handleChangeContextFactors() {
        this.showStartScreen();
        this.startScreenView.visible = false;
        this.contextFactorsView.visible = true;
        this.updateContent(this.content);
        this.updateSidebar();
    }

    handleChangeElementName(element, value) {
        this.process.changeElementName(element, value);
        this.updateContent();
        this.updateSidebar();
    }

    handleDeleteElement(element) {
        this.informationView.deleteElementModal(this.content, element);
    }

    handleDeleteElementModalAction(element = null) {
        if (element) {
            this.process.deleteElement(element);
            this.informationView.element = null;
            this.informationView.visible = false;
        }
        this.updateContent();
        this.updateSidebar();
    }

    handleChangeColor(activity, value) {
        this.process.changeColor(activity, value);
        this.updateContent();
        this.updateSidebar();
    }

    handleChangeDetailLevel(activity, value) {
        this.process.changeDetailLevel(activity, value);
        this.updateContent();
        this.updateSidebar();
    }

    handleChangeDuration(activity, value) {
        this.process.changeDuration(activity, value);
        this.updateSidebar();
    }

    handleAddTask(activity, value) {
        this.process.addTask(activity, "user", value);
        this.updateContent();
        this.updateSidebar();
    }

    handleAddInfo(activity, value) {
        this.process.addInfo(activity, "user", value);
        this.updateSidebar();
    }

    handleRemoveTask(activity, value) {
        this.process.removeTask(activity, value);
        this.updateSidebar();
    }

    handleRemoveInfo(activity, value) {
        this.process.removeInfo(activity, value);
        this.updateSidebar();
    }
}

export default Controller;