import inputs from '../../../config/inputs.json';
import directDecisions from '../../../config/direct-decisions.json';
import decisionMatrices from '../../../config/decision-matrices.json';

class ContextFactors {

    constructor() {
        this._inputs = inputs;

        this._directDecisions = directDecisions;
        this._decisionMatrices = decisionMatrices;

        this._decisionValues =  new Map();
        this._decisionPointer = 0;
        this._modifications = [];

        this.initDecisionValues();
    }

    
    get inputs() {
        return this._inputs.inputs;
    }

    get phaseInputs() {
        return this._inputs.phaseInputs;
    }
    
    get systemInputs() {
        return this._inputs.systemInputs;
    }
    
    get directDecisions() {
        return this._directDecisions;
    }
    
    get decisionMatrices() {
        return this._decisionMatrices;
    }
    
    get decisionValues() {
        return this._decisionValues;
    }

    set decisionValues(value) {
        this._decisionValues = value;
    }
    
    get decisionPointer() {
        return this._decisionPointer;
    }
    
    set decisionPointer(value) {
        this._decisionPointer = value;
    }
    
    get modifications() {
        return this._modifications;
    }

    set modifications(value) {
        this._modifications = value;
    }

    contextFactorsSerialize() {
        return Object.fromEntries(this.decisionValues);
    }
    
    getInputByPointer() {
        if ((this.decisionPointer <= 0) || (this.decisionPointer > this.inputs.length)) {
            return null;
        }
        else {
            return this.inputs[this.decisionPointer - 1];
        }
    }

    initDecisionValues() {
        this.inputs.forEach( input => {
            this.updateDecisionValues(input.id, input.defaultIndex);
        });
        this.phaseInputs.forEach( input => {
            this.updateDecisionValues(input.id, input.defaultIndex);
        });
        this.systemInputs.forEach( input => {
            this.updateDecisionValues(input.id, input.defaultIndex);
        });
    }

    updateDecisionValues(key, value) {
        this.decisionValues.set(key, value);
    }

    /**
     * Starts the decision making process by resolving the root-nodes of each decision tree.
     */
    makeDecisions() {

        // reset modification Array
        this.modifications = [];

        // list of decisions
        this.resolve("decision01");
        this.resolve("decision011");
        this.resolve("decision021");
        //this.resolve("decision022");
        this.resolve("decision041");
        this.resolve("mp06-iterativePlanning");
        this.resolve("decision05");
        this.resolve("decision06");
        this.resolve("decision07");
    }

    /**
     * Determines the best alternative of a decision problem.
     * @param {Object} decisionProblem The Problem to be solved
     */
    resolve(decisionProblem) {
        if(decisionProblem.charAt(0) == "m") {
            this.resolveMatrix(this.decisionMatrices[decisionProblem]);
        }
        else {
            this.resolveDirectDecision(this.directDecisions[decisionProblem]);
        }
    }

    /**
     * Determines the best alternative of a decision matrix.
     * @param {Object} matrix The matrix to be solved
     */
    resolveMatrix(matrix) {
        let sums = [];
        let weights = [];

        for(let i = 0; i < matrix.influences.length; i++) {
            
            // Alle Inputs in eine Obkjekt Array zusammenfassen
            let inputList = this.inputs.concat(this.phaseInputs).concat(this.systemInputs);

            // id des influences in variable speichern
            let influenced = matrix.influences[i];

            // 1. nimm vom Influenced Input den Value, der vom User im Formular ausgewählt wurde
            let influencedValue = inputList.find(obj => obj.id == influenced).values[this.decisionValues.get(influenced)];

            // 2. nimm den größten Value des Influenced Input
            let maxInfluencedValue = Math.max(...(inputList.find(obj => obj.id == influenced).values));

            weights.push(influencedValue / maxInfluencedValue);
        }
        
        for(let i = 0; i < matrix.alternatives.length; i++) {
            let sum = 0;
            for(let j = 0; j < matrix.alternatives[i].values.length; j++) {
                sum += weights[i]*matrix.alternatives[i].values[j];
            }
            sums.push(sum);
        }

        let winner =  matrix.alternatives[sums.indexOf(Math.max(...sums))];

        if (winner.operations) {
            winner.operations.forEach(operation => this.modifications.push(operation));
        }
        (winner.nextDecision) ? this.resolve(winner.nextDecision) : null;
    }
    
    /**
     * Determines the best alternative of a direct decision
     * @param {Object} decisionProblem  The decisionProblem to be solved
     */
    resolveDirectDecision(decisionProblem) {
        let winner = decisionProblem.alternatives[this.decisionValues.get(decisionProblem.input)];
        if (winner.operations) {
            winner.operations.forEach(operation => this.modifications.push(operation));
        }
        (winner.nextDecision) ? this.resolve(winner.nextDecision) : null;
    }

    /**
     * Applies modifications to the generic development process.
     * @param {Array} operationList  The Array which contains the modifications.
     */
    /*applyModification(operationList) {
        console.log("hi");
        operationList.forEach(modification => {
            var node = this.process.getNodeByID(modification.phase);
            if(node) {
                if(modification.duration){
                    node.duration = {gen: ["none", "short", "middle", "long"].indexOf(modification.duration), amount: 0, unit: "d"};
                }

                if(modification.tasks){
                    modification.tasks.forEach(task => {
                        node.addTask(Task.systemTask(task));
                    });
                }

                if(modification.info){
                    modification.info.forEach(i => {
                        node.addInfo(i);
                    });
                }
            }
        });
    }*/

}

export default ContextFactors;