import { ActivityNode } from '../js/modules/process/processModules/Node';
import Memory from './modules/process/processModules/Memory';

import dragScroll from '../svg/dragScroll.svg';
import fluidSingle from '../svg/fluidSingle.svg';
import fluidMultiple from '../svg/fluidMultiple.svg';
import solidSingle from '../svg/solidSingle.svg';
import solidMultiple from '../svg/solidMultiple.svg';
import fluidActivity from '../svg/fluidActivity.svg';
import solidActivity from '../svg/solidActivity.svg';
import closeIcon from '../svg/close.svg';

class View {

    /**
     * Creates a user interface element of the editor
     */
    constructor() {
        this._root = document.createElement("div");
        this._visible = false;
        this._parentDOM = null;
    }

    get root() {
        return this._root;
    }
    
    set root(root) {
        this._root = root;
    }

    get visible() {
        return this._visible;
    }

    set visible(value) {
        this._visible = value;
    }

    get parentDOM() {
        return this._parentDOM;
    }

    set parentDOM(parent) {
        this._parentDOM = parent;
    }

    /**
     * Returns an HTML element that contains the rendered view
     * @returns {HTMLElement} user interface element.
     */
    render() {
        return undefined;
    }

    /**
     * Returns user interface element if visibility is true
     *
     * @param {HTMLElement} parentDOM The number to raise.
     * @return {HTMLElement} user interface element.
     */
    toggle(parentDOM) {
        this.root.innerHTML = "";
        if (this.visible) {
            this.parentDOM = parentDOM;
            return this.render();
        } 
        else { return null; }
    }

    /**
     * Helper function to create HTML elements
     *
     * @param {String} type HTML element name.
     * @param {String} id HTML element id.
     * @param {Array} classes Array of HTML element classes.
     * @return {HTMLElement}
     */
    createElement(type, id = null, classes = []) {
        const element = document.createElement(type);
        (id) ? element.id = id : null;
        classes.forEach(c => element.classList.add(c) );
        return element;
    }

    /**
     * Converts a hex color to a rgba color and determines the color saturation
     *
     * @param {number} hex hex color.
     * @param {number} scale color saturation: 1 = low, 2 = middle, 3 = high
     * @return {number} x raised to the n-th power.
     */
    getColor(hex, scale) {
        let c;
        let percent;
        
        if (scale == 1) { percent = 0.2; }
        else if (scale == 2) { percent = 0.5 }
        else if (scale == 3) { percent = 1 }
        
        if(/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)){
            c= hex.substring(1).split('');
            if(c.length== 3){
                c= [c[0], c[0], c[1], c[1], c[2], c[2]];
            }
            c= '0x'+c.join('');
            return 'rgba('+[(c>>16)&255, (c>>8)&255, c&255].join(',')+',' + percent +')';
        }
        throw new Error('Bad Hex');
    }

    /**
     * Creates a model window for activities and memories
     *
     * @param {number} title Title text of the modal window.
     * @param {Element} element Main actor of the model window: activity or memory
     * @param {HTMLElement} action This HTML element is displayed: input or paragraph.
     * @param {number} cancelBtn Pass a button with the cancel action.
     * @param {number} submitBtn pass a button with the submit action.
     * @return {HTMLElement}
     */
    createModal(title, element, action, cancelBtn, submitBtn) {

        let icon = this.getIcon(element);

        const modal = document.createElement("div");
        modal.classList.add("modal");

        const container = document.createElement("div");
        container.classList.add("modalContainer");
        modal.appendChild(container);
        
            cancelBtn.classList.add("cancelBtn");
            cancelBtn.classList.add("icon");
            cancelBtn.setAttribute("src", closeIcon);
            container.appendChild(cancelBtn);

            const text = document.createElement("span");
            text.classList.add("title");
            text.innerHTML = title;
            container.appendChild(text);

            const img = document.createElement("img");
            img.classList.add("elementIcon");
            img.setAttribute("src", icon);
            container.appendChild(img);

                // action handling
                if (action.tagName.toLowerCase() == "input") {
                    action.placeholder = "enter a name";
                    container.appendChild(action);
                }
                else if (action.tagName.toLowerCase() == "p") {
                    container.appendChild(action);
                }

            submitBtn.classList.add("submitBtn");
            submitBtn.textContent = "Okay";
            container.appendChild(submitBtn);

            return modal;
    }

    /**
     * Pass an element (activity or memory) and get back a matching svg icon
     *
     * @param {Element} element activity or memory
     * @param {number} n The power, must be a natural number.
     * @return {SVGElement} icon
     */
    getIcon(element) {
        let icon = dragScroll;
        if (element instanceof ActivityNode) {
            (element.aggregate == "fluid") ? icon = fluidActivity : icon = solidActivity;
        }
        else if (element instanceof Memory){
            if (element.aggregate == "fluid") { (element.type == "single") ? icon = fluidSingle : icon = fluidMultiple; }
            else { (element.type == "single") ? icon = solidSingle : icon = solidMultiple; }
        }
        return icon;
    }

    /**
     * Creates a HTML file loader to upload files
     *
     * @param {EventObserver} clickImport EventObserver that is executed as an action
     * @return {HTMl Element} file loader
     */
    getFileLoader(clickImport) {
        const fileLoaderContainer = document.createElement("div");
        fileLoaderContainer.classList.add("fileLoaderContainer");

        const fileLoader = document.createElement("input");
        fileLoader.setAttribute("type", "file");
        fileLoader.setAttribute("id", "fileLoader");
        fileLoader.setAttribute("accept", ".json");
        fileLoaderContainer.appendChild(fileLoader);
        fileLoader.addEventListener('change', () => { clickImport.trigger(fileLoader.files[0]);});
        const label = document.createElement("label");
        label.setAttribute("for", "fileLoader");
        fileLoaderContainer.appendChild(label);

        return fileLoaderContainer;
    }

    /**
     * CSS action: shows the input and oupit zones
     */
    zonesInForeground() {
        let zones = document.getElementsByClassName("zone");
            for (let z of zones) {
                z.classList.add("toForeground", "dragStart");
                z.classList.remove("toBackground")
            }
    }

    /**
     * CSS action: hides the input and oupit zones
     */
    zonesInBackground() {
        let zones = document.getElementsByClassName("zone");
            for (let z of zones) {
                z.classList.remove("toForeground", "dragStart");
                z.classList.add("toBackground")
            }
    }

    /**
     * CSS action: shows the areas
     */
    areasInForeground() {
        let areas = document.getElementsByClassName("area");
            for (let a of areas) {
                a.classList.add("dragStart");
            }
    }

    /**
     * CSS action: hides the areas
     */
    areasInBackground() {
        let zones = document.getElementsByClassName("area");
            for (let z of zones) {
                z.classList.remove("dragStart");
            }
    }
}

export default View;