import View from '../../View'
import './controlPanel.css'
import EventObserver from '../../EventObserver';

import dragScroll from '../../../svg/dragScroll.svg';
import fluidSingle from '../../../svg/fluidSingle.svg';
import fluidMultiple from '../../../svg/fluidMultiple.svg';
import solidSingle from '../../../svg/solidSingle.svg';
import solidMultiple from '../../../svg/solidMultiple.svg';
import fluidActivity from '../../../svg/fluidActivity.svg';
import solidActivity from '../../../svg/solidActivity.svg';

class controlPanelView extends View {

    /**
     * Creates a toolbar instance
     */
    constructor() {
        super();

        this._dragScroll = true;

        this.passDragObject = new EventObserver();
        this.addActivityModalAction = new EventObserver();
        this.addMemoryModalAction = new EventObserver();
    }

    get dragScroll() {
        return this._dragScroll;
    }

    set dragScroll(value) {
        this._dragScroll = value;
    }

    /**
     * Creates the HTML element of a toolbar
     *
     * @return {HTMLElement}
     */
    render() {
        const controlPanel = document.createElement("div");
        controlPanel.id = "controlPanel";
        this.parentDOM.appendChild(controlPanel);

        this.renderAddInterface(this.parentDOM);

        return controlPanel;
    }

    /**
     * Helper function to create html elements of the toolbar. Creates seven icons: Drag control, add role, add roles, add document, add documents, add activity, add meeting.
     *
     * @param {number} x The number to raise.
     * @param {number} n The power, must be a natural number.
     * @return {number} x raised to the n-th power.
     */
    renderAddInterface(parentDOM) {

        // ADD-INTERFACE

        const addInterface = document.createElement("div");
        addInterface.id = "addInterface";
        parentDOM.appendChild(addInterface);

            // drag-scroll display
            const dragScrollContainer = document.createElement("div");
            dragScrollContainer.id = "dragScrollContainer";
            addInterface.appendChild(dragScrollContainer);
            
            const dragScrollImg = document.createElement("img");
            dragScrollImg.classList.add("dragScrollImg");
            dragScrollImg.setAttribute("src", dragScroll);
                dragScrollContainer.appendChild(dragScrollImg);

            // add memories
            const addMemories = document.createElement("div");
            addMemories.id = "addMemories";
            addInterface.appendChild(addMemories);

                addMemories.appendChild(this.createAddButton("fluidSingleMemory", "Add Role", fluidSingle));
                addMemories.appendChild(this.createAddButton("fluidMultipleMemory", "Add Roles", fluidMultiple));
                addMemories.appendChild(this.createAddButton("solidSingleMemory", "Add Document", solidSingle));
                addMemories.appendChild(this.createAddButton("solidMultipleMemory", "Add Documents", solidMultiple));

            // add activities
            const addActivities = document.createElement("div");
            addActivities.id = "addActivities";
            addInterface.appendChild(addActivities);

            addActivities.appendChild(this.createAddButton("fluidActivity", "Add Meeting", fluidActivity));
            addActivities.appendChild(this.createAddButton("solidActivity", "Add Activity", solidActivity));

        return addInterface;
    }

    /**
     * Creates a draggable icon/button for the toolbar
     *
     * @param {String} title instruction for drag action
     * @param {String} alt hover text of icon
     * @param {String} icon svg icon to drag
     * @return {HTMLElement} button
     */
    createAddButton(title, alt, icon, action = null) {
        const button = document.createElement("div");
        button.id = title;
        button.setAttribute("alt", alt);
        button.setAttribute("draggable", "true");
        button.addEventListener('dragstart', (event) => {
            this.dragAction(event, title);
        });

        const img = document.createElement("img");
        img.setAttribute("src", icon);
        button.appendChild(img);

        return button;
    }

    /**
     * Helper function for the drag action. Sends the drag instruction
     *
     * @param {Event} event Event to transmit the instruction
     * @param {String} instruction drag instruction, so that every drag recipient knows if he should implement the drag action
     */
    dragAction(event, instruction) {
        this.passDragObject.trigger({"instruction": instruction, "data": null});
        event.dataTransfer.setData("text", instruction);
    }

    /**
     * When an element is selected in the process, the dragScroll icon is displayed as blocked.
     *
     * @param {HTMLElement} buttonDOM HTML element of the dragScroll icons
     * @param {HTMLElement} target selected element in the process
     */
    updateDragScroll(buttonDOM, target) {
        if (!buttonDOM) return;
        
        // init
        buttonDOM.classList.remove("dragScrollBlocked");

        // add / remove class
        (target) ? this.dragScroll = false : this.dragScroll = true;
        if (this.dragScroll) {
            buttonDOM.classList.remove("dragScrollBlocked");
        }
        else {
            buttonDOM.classList.add("dragScrollBlocked");
        }
    }

    /**
     * Creates a modal window for creating an activity
     *
     * @param {Activity} activity Activity to be created
     * @param {String} parentId ID of the parent node
     * @param {String} parentSource Is parent a dynamically created structure node or is it stored in the process tree: dynamic or static
     * @param {String} arrangement Sequentiell oder parallel
     * @param {Nodes} siblings Siblings of the activity
     * @param {number} position Position / order der Aktivität im Baum
     */
    addActivityModal(activity, parentId, parentSource, arrangement, siblings, position) {
        
        const input = document.createElement("input");

        const cancelBtn = document.createElement("img");
        cancelBtn.addEventListener('click', () => {
                this.addActivityModalAction.trigger();
        });
        
        const submitBtn = document.createElement("button");
        submitBtn.addEventListener('click', () => {
            (input.value == "") ? activity.name = "New Activity" : activity.name = input.value;
            this.addActivityModalAction.trigger(activity, parentId, parentSource, arrangement, siblings, position, true);
        });

        let modal = this.createModal("Create Activity", activity, input, cancelBtn, submitBtn);
        this.parentDOM.appendChild(modal);
    }

    /**
     * Creates a modal window for creating an activity
     *
     * @param {Memory} memory Memory to be created
     * @param {Node} parent Parent of the memory
     */
    addMemoryModal(memory, parent) {
        
        const input = document.createElement("input");

        const cancelBtn = document.createElement("img");
        cancelBtn.addEventListener('click', () => {
                this.addMemoryModalAction.trigger();
        });
        
        const submitBtn = document.createElement("button");
        submitBtn.addEventListener('click', () => {
            (input.value == "") ? memory.name = "New Memory" : memory.name = input.value;
            this.addMemoryModalAction.trigger(memory, parent, true);
        });

        let modal = this.createModal("Create Memory", memory, input, cancelBtn, submitBtn);
        this.parentDOM.appendChild(modal);
    }
}

export default controlPanelView;