import { ContainerNode, ActivityNode } from "./processModules/Node";
import Area from "./processModules/Area";
import Memory from "./processModules/Memory";
import initialProcess from '../../../config/initialProcess.json';

import { v4 as uuidv4 } from 'uuid';

class Process {

    constructor(name) {
        this._name = name;
        this._activeElement = null;
        this._root = null;
        this._relations = null;
    }

    get name() {
        return this._name;
    }

    set name(name) {
        this._name = name;
    }

    get activeElement() {
        return this._activeElement;
    }

    set activeElement(value) {
        this._activeElement = value;
    }
    get root() {
        return this._root;
    }

    set root(root) {
        this._root = root;
    }

    get relations() {
        return this._relations;
    }

    set relations(relations) {
        this._relations = relations;
    }

    // --------------------------------------------------------------------
    // general
    // --------------------------------------------------------------------

    getId() {
        return "id" + uuidv4();
    }

    // --------------------------------------------------------------------
    // JSON -> Process
    // --------------------------------------------------------------------
    
    setInitialProcess(process = initialProcess) {
        return this.objectToProcess(process);
    }

    objectToProcess(obj) {
        let objectIsProcess = false;
        try {
            this.name = obj.name;
            this.root = this.objectToRoot(obj.root);
            this.relations = this.objectToRelations(obj.relations);
            objectIsProcess = true;
        }
        catch (e) { 
            console.error("Invalid Graph Format :(");
            this.setInitialProcess();
        }
        finally {
            return objectIsProcess;
        }
    }

    objectToRoot(obj) {
        let node = null;
        let children = [];

        
        if (obj.type == "container") {
            node = new ContainerNode(obj.id, obj.arrangement);
        }
        else if (obj.type == "activity") {
                node = new ActivityNode(obj.id, obj.name, obj.aggregate, obj.phase, obj.duration, obj.levelOfDetail, [], [], obj.color);
                (obj.tasks) ? node.addTasks(obj.tasks, "system") : null;
                (obj.infos) ? node.addInfos(obj.infos, "system") : null;
            }
            
            obj.children.forEach( child => {
                children.push(this.objectToRoot(child));
            });
            
            (node) ? node.children = children : null;

        return node;
    }

    objectToRelations(obj) {
        let relations = [];

        obj.forEach( a => {
            let area = new Area(a.id, a.parent);
            let memories = [];
            a.memories.forEach( m => {
                memories.push(new Memory(m.id, m.name, m.type, m.aggregate, m.category, m.inputs, m.outputs))
            });
            area.memories = memories;
            relations.push(area);
        });

        return relations;
    }

    // --------------------------------------------------------------------
    // Process -> JSON
    // --------------------------------------------------------------------

    export(decisionValues) {
        let dataStr = this.processToJSON(decisionValues);
        let dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr);
        let exportFileDefaultName = this.name + '.json';
        let linkElement = document.createElement('a');
        linkElement.setAttribute('href', dataUri);
        linkElement.setAttribute('download', exportFileDefaultName);
        linkElement.click();
    }

    processToJSON(decisionValues) {
        let process = JSON.stringify({name: this.name, decisionValues: decisionValues, root: this.root.nodeSerialize(), relations: this.relationsSerialize()});
        return process;
    }

    relationsSerialize() {
        let relations = [];
        this.relations.forEach(area => {
            relations.push(area.areaSerialize());
        })
        return relations;
    }

    // --------------------------------------------------------------------
    // Modifications
    // --------------------------------------------------------------------

    modifyProcess(modifications) {
        this.makeModifications(modifications);
        this.garbageCollector(this.root);
    }
    
    makeModifications(modifications, node = this.root, parent = null) {
        modifications.forEach( m => {
            if (m.phase == node.id) {
                if (m.duration) {
                    (m.duration == "none") ? this.removeNode(node, parent) : node.duration = m.duration;
                }
                (m.tasks) ? node.addTasks(m.tasks, "system") : null;
                (m.info) ? node.addInfos(m.info) : null;
            }
        });
    
        node.children.forEach( child => this.makeModifications(modifications, child, node));
    }

    garbageCollector(node, parent = null, grandparent = null) {
        if (node && (node.duration === 0)) {
            if (parent.children.length == 1) {
                this.removeNode(parent, grandparent)
            }
            else {
                this.removeNode(node, parent);
            }
        }

        node.children.forEach(child => {
            this.garbageCollector(child, node, parent);
        });
    }

    // --------------------------------------------------------------------
    // Update functions
    // --------------------------------------------------------------------

    updateActiveElement(element) {
        this.activeElement = element;
    }
    
    removeArea(id) {
        this.relations = this.relations.filter( area => ( area.id != id ));
    }

    removeNode(node, parent) {
        let children = parent.children; 

        let position = children.findIndex(child => child.id == node.id);

        children = children.filter(x => { return x.id != node.id; });
        parent.children = children;

        return position;
    }

    removeNodeById(id, root = this.root) {
        root.children.forEach( child => {
            if (child.id == id) {
                this.removeNode(child, root);
            }
            else {
                this.removeNodeById(id, child);
            }
        })
    }

    addNode(node, parent, position = -1) {
        if ((node != null) && (parent != null)) {
            if ((position == -1) || (position > parent.children.length)) {
                parent.children.push(node);
            }
            else { parent.children.splice(position, 0, node); }
        }
        else { console.log("Error: No node was added"); }
    }

    getNode(id, node = this.root, result = null) {
        if (node.id == id) {
            result = node;
            return node;
        }
        else {
            node.children.forEach( child => {
                let res = this.getNode(id, child, result);
                if (res != null) {
                    result = res;
                }
            });
        }
        return result;
    }

    createActivity(instruction, name = "", phase = "", duration = 0, level = 0, tasks = [], infos = []) {
        let id = this.getId();
        let aggregate; 
        (instruction == "fluidActivity") ? aggregate = "fluid" : aggregate = "solid";
        
        return new ActivityNode(id, name, aggregate, phase, duration, level, tasks, infos);
    }

    addActivity(activity, parentId, parentSource, arrangement, siblings, position) {
        let parent = this.getNode(parentId);

        if (parentSource == "static") { this.addNode(activity, parent, position); }
        else {
            let container = new ContainerNode(this.getId(), arrangement);

            let sibling = this.getNode(siblings[0], parent);
            let pos = this.removeNode(sibling, parent);
            this.addNode(sibling, container, -1);
            this.addNode(container, parent, pos);

            this.addNode(activity, container, position);
        }
    }

    // --------------------------------------------------------------------
    // Areas, memories & relationships
    // --------------------------------------------------------------------

    shiftMemory(memory, oldId, newId, parent, relationId, relationType) {

        let inputs = [];
        let outputs = [];
        (relationType == "input") ? inputs.push(relationId) : outputs.push(relationId);
        
        let newMemory = new Memory(this.getId(), memory.name, memory.type, memory.aggregate, memory.category, inputs, outputs);
        this.addToArea(newId, newMemory, parent);

        this.removeFromArea(oldId, memory, relationId, relationType);
    }

    addToArea(areaId, memory, parent) {
        let pos = this.findArea(areaId);
        if (pos > -1 ) {
            this.relations[pos].addMemory(memory);
        }
        else {
            let area = new Area(areaId, parent);
            this.relations.push(area);
            area.memories.push(memory);
        }
    }

    removeFromArea(areaId, memory, relationId, relationType) {
        let pos = this.findArea(areaId);
        if (pos > -1 ) {
            let size = this.relations[pos].removeRelationFromMemory(memory.id, relationId, relationType);
            if (size == 0) {
                this.relations[pos].removeMemory(memory.id);
            }
        }
    }

    findArea(id) {
        return this.relations.findIndex((area) => area.id == id ); 
    }

    isChild(id, parent, node = this.root, family = [], result = false) {
        if (node.id == parent) {
            family = [];
        }
        if (family.find(x => x == parent) > -1) {
            result = true;
        }
        family.push(node.id);

        node.children.forEach(child => {
            return this.isChild(id, parent, child, family, result);
        });
        return result;
    }

    addMemory(instruction, activityId, direction, parent) {
        let id = this.getId();
        let name = "New Memory";
        let type = "single";
        let aggregate = "fluid";
        let category = this.getId();
        let inputs = [];
        let outputs = [];

        switch(instruction) {
            case "fluidSingleMemory" :
                type = "single";
                aggregate = "fluid";
                (direction == "input") ? inputs.push(activityId) : outputs.push(activityId);
                break;
            case "fluidMultipleMemory" :
                type = "multiple";
                aggregate = "fluid";
                (direction == "input") ? inputs.push(activityId) : outputs.push(activityId);
                break;
            case "solidSingleMemory" :
                type = "single";
                aggregate = "solid";
                (direction == "input") ? inputs.push(activityId) : outputs.push(activityId);
                break;
            case "solidMultipleMemory" :
                type = "multiple";
                aggregate = "solid";
                (direction == "input") ? inputs.push(activityId) : outputs.push(activityId);
                break;
        }
        
        let newMemory = new Memory(id, name, type, aggregate, category, inputs, outputs);
        this.addToArea("root", newMemory, parent);
    }

    createMemory(instruction, activityId, direction, parent) {

        let id = this.getId();
        let name = "New Memory";
        let type = "single";
        let aggregate = "fluid";
        let category = this.getId();
        let inputs = [];
        let outputs = [];

        switch(instruction) {
            case "fluidSingleMemory" :
                type = "single";
                aggregate = "fluid";
                (direction == "input") ? inputs.push(activityId) : outputs.push(activityId);
                break;
            case "fluidMultipleMemory" :
                type = "multiple";
                aggregate = "fluid";
                (direction == "input") ? inputs.push(activityId) : outputs.push(activityId);
                break;
            case "solidSingleMemory" :
                type = "single";
                aggregate = "solid";
                (direction == "input") ? inputs.push(activityId) : outputs.push(activityId);
                break;
            case "solidMultipleMemory" :
                type = "multiple";
                aggregate = "solid";
                (direction == "input") ? inputs.push(activityId) : outputs.push(activityId);
                break;
        }
        
        let newMemory = new Memory(id, name, type, aggregate, category, inputs, outputs);

        return newMemory;
    }

    duplicateMemory(activityId, direction) {

        let id = this.getId();
        let name = this.dragObject.data.name;
        let type = this.dragObject.data.type
        let aggregate = this.dragObject.data.aggregate;
        let category = this.dragObject.data.category;
        let inputs = [];
        let outputs = [];
        (direction == "output") ? outputs.push(activityId) : inputs.push(activityId);
    
        let newMemory = new Memory(id, name, type, aggregate, category, inputs, outputs);
        return newMemory;
    }

    duplicateActiveMemory() {

        if (!this.activeElement instanceof Memory) { return null; }

        let id = this.activeElement.id;
        let name = this.activeElement.name;
        let type = this.activeElement.type
        let aggregate = this.activeElement.aggregate;
        let category = this.activeElement.category;
        let inputs = this.activeElement.inputs;
        let outputs = this.activeElement.outputs;
    
        let newMemory = new Memory(id, name, type, aggregate, category, inputs, outputs);
        return newMemory;
    }

    // --------------------------------------------------------------------
    // Execute Sidebar Actions
    // --------------------------------------------------------------------

    changeProcessName(name) {
        this.name = name;
    }

    changeElementName(element, value) {
        element.name = value;
    }

    deleteElement(element) {
        if(element instanceof ActivityNode) {
            this.removeActivity(element);
        }
        else if (element instanceof Memory) {
            this.removeMemory(element);
        }
    }

    removeActivity(activity) {
        activity.children.forEach(child => {
            this.removeActivity(child);
        });
            this.removeNodeById(activity.id);
            this.relations.forEach(area => {
                area.removeActivityRelation(activity.id);
            })
    }

    removeMemory(memory) {
        this.relations.forEach(area => {
            area.removeMemory(memory.id);
        });
    }

    changeColor(activity, value) {
        activity.color = value;
        (activity.levelOfDetail == 0) ? activity.levelOfDetail = 3 : null;
    }

    changeDetailLevel(activity, value) {
        activity.levelOfDetail = value;
    }

    changeDuration(activity, duration) {
        activity.duration = duration;
    }

    addTask(activity, source, text) {
        activity.addTask(source, text);
    }

    removeTask(activity, task) {
        activity.removeTask(task);
    }

    addInfo(activity, source, text) {
        activity.addInfo(source, text);
    }

    removeInfo(activity, info) {
        activity.removeInfo(info);
    }

}

export default Process