class Memory {

    constructor(id, name, type = "fluid", aggregate = "single", category = "", inputs = [], outputs = []) {
        this._id = id;
        this._name = name;
        this._type = type;
        this._aggregate = aggregate;
        this._category = category;
        this._inputs = inputs;
        this._outputs = outputs;
    }

    get id() {
        return this._id;
    }
    
    set id(id) {
        this._id = id;
    }

    get name() {
        return this._name;
    }
    
    set name(name) {
        this._name = name;
    }

    get type() {
        return this._type;
    }
    
    set type(type) {
        this._type = type;
    }

    get aggregate() {
        return this._aggregate;
    }

    set aggregate(aggregate) {
        this._aggregate = aggregate;
    }

    get category() {
        return this._category;
    }
    
    set category(category) {
        this._category = category;
    }

    get inputs() {
        return this._inputs;
    }
    
    set inputs(inputs) {
        this._inputs = inputs;
    }

    get outputs() {
        return this._outputs;
    }
    
    set outputs(outputs) {
        this._outputs = outputs;
    }

    memorySerialize() {
        return {id: this.id, name: this.name, type: this.type, aggregate: this.aggregate, category: this.category, inputs: this.inputs, outputs: this.outputs};
    }

    addInput(id) {
        (this.inputs.findIndex(i => i == id) < 0) ? this.inputs.push(id) : null;
    }

    removeInput(id) {
        this.inputs = this.inputs.filter( i => i != id);
    }

    addOutput(id) {
        (this.outputs.findIndex(o => o == id) < 0) ? this.outputs.push(id) : null;
    }

    removeOutput(id) {
        this.outputs = this.outputs.filter( o => o != id);
    }


}

export default Memory;